import React from 'react';
import { userAuth, teacherAuth, loginWithToken } from './support/Auth';
import hello from 'hello';
import { Router, Route, IndexRoute } from 'react-router';
import App from './components/layouts/App';
import Home from './components/main/Home';
import StoryList from './components/stories/StoryList';
import Story from './components/stories/Story';
import ResetPassword from './components/sessions/ResetPassword';
import Login from './components/sessions/Login';
import Edit from './components/users/Edit';
import NewStory from './components/stories/NewStory';
import User from './components/users/User';
import ManageLabels from './components/labels/ManageLabels';
import NotFound from './components/notFound/NotFound';
import UserPanel from './components/admin/UserPanel';
import NewUsers from './components/admin/NewUsers';
import UserStats from './components/users/UserStats';
import Drafts from './components/stories/Drafts';
import Analytics from './support/Analytics';
import ParseAuth from './support/ParseAuth';
import NavigationBar from './components/navigation/NavigationBar';

export default (
  <Route path="/" component={App}>
    <IndexRoute components={{main: Home, navbar: NavigationBar.Index}} />
    <Route path="admin"
      onEnter={teacherAuth}
    >
      <Route
        path="users"
        components={{main: UserPanel, navbar: NavigationBar.UserPanel}}
      />
      <Route
        path="users/new"
        components={{main: NewUsers, navbar: NavigationBar.NewUsers}}
      />
      <Route
        path="labels"
        components={{main: ManageLabels, navbar: NavigationBar.Labels}}
      />
    </Route>
    <Route
      path="/users/:id/stats"
      components={{main: UserStats, navbar: NavigationBar.UserStats}}
      onEnter={userAuth}
    />
    <Route
      path="/stories"
      components={{main: StoryList, navbar: NavigationBar.Stories}}
      onEnter={userAuth}
    >
      <Route path="/stories/bookmarks" />
      <Route path="/stories/search" />
    </Route>
    <Route
      path="/stories/drafts"
      components={{main: Drafts, navbar: NavigationBar.Stories}}
      onEnter={userAuth}
    />
    <Route
      path="/stories/new"
      components={{main: NewStory, navbar: NavigationBar.NewStory}}
      onEnter={userAuth}
    />
    <Route
      path="/stories/:id"
      components={{main: Story, navbar: NavigationBar.Story}}
      onEnter={userAuth}
    />
    <Route
      path="/forgot"
      components={{main: ResetPassword, navbar: NavigationBar.Stories}}
    />
    <Route
      path="/login"
      components={{main: Login, navbar: NavigationBar.Stories}}
      onEnter={loginWithToken}
    />
    <Route
      path="/logout"
      onEnter={(nextState, replace) => {
        Analytics.track('User', 'Logout');

        ParseAuth.logOut(() => {
          hello('google').logout();
          replace('/');
        });
      }}
    />
    <Route
      path="/me/edit"
      components={{main: Edit, navbar: NavigationBar.Profile}}
      onEnter={userAuth}
    />
    <Route
      path="/users/:id"
      components={{main: User, navbar: NavigationBar.User}}
      onEnter={userAuth}
    />
    <Route path="*" components={{main: NotFound}} />
  </Route>
);



