import React from 'react';
import ReactDOM from 'react-dom';
import {Bar} from 'react-chartjs';

export default class LineChart extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      resize: false,
    };

    this.resizeCanvas = this.resizeCanvas.bind(this);
  }

  componentDidMount() {
    const dom = ReactDOM.findDOMNode(this.refs.line);
    const ctx = dom.getContext('2d');
    const gradient = ctx.createLinearGradient(0,0,0,400);
    gradient.addColorStop(0, this.props.startGradientColor);
    gradient.addColorStop(1, this.props.endGradientColor);
    this.gradient = gradient;
    this.forceUpdate();
  }

  resizeCanvas() {
    this.setState({resize: true});
  }

  render() {
    const data = this.props.data;

    if (data.datasets && data.datasets.length) {
      const datasets = data.datasets.map(dataset => {
        const newObject = Object.create(dataset);
        newObject.fillColor = this.gradient;
        return newObject;
      });
      data.datasets = datasets;
    }

    return (
      <Bar
        data={data}
        options={{
          pointDot: false,
          dataStroke: false,
          scaleFontColor: '#FFF',
          multiTooltipTemplate: '<%if (datasetLabel){%><%=datasetLabel%>: <%}%><%= value %>',
        }}
        redraw={this.gradient || this.state.resize}
        ref="line"
      />
    );
  }
}



