import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import UserTypeConstants from '../../constants/UserTypeConstants';
import _ from 'lodash';
import classNames from 'classnames';
import Analytics from '../../support/Analytics';
import StoryHeader from './StoryHeader';
import EditableStoryHeader from './EditableStoryHeader';
import PageRow from '../pages/PageRow';
import Submissions from '../pages/Submissions';
import NewSubmission from '../pages/NewSubmission';
import ApproveSlider from '../pages/ApproveSlider';
import WriteAssistButton from './WriteAssistButton';
import LabelTypeConstants from '../../constants/LabelTypeConstants';
import PageStatusConstants from '../../constants/PageStatusConstants';
import Labels from './Labels';
import pubnub from 'pubnub';
import secrets from '../../../config/secrets';
import EnglishReports from '../clio/EnglishReports';
import NotificationSystem from '../widgets/NotificationSystem';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

export default class Story extends ParseComponent {
  constructor(props) {
    super(props);
    this.data = {
      stories: props.stories,
      pages: props.pages,
      likes: props.likes,
      user: props.user
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleLabelChange = this.handleLabelChange.bind(this);
    this.handleClickWriteAssist = this.handleClickWriteAssist.bind(this);
    this.handleCharacterLimitChange = this.handleCharacterLimitChange.bind(this);
    this.handlePendingEdit = this.handlePendingEdit.bind(this);
    this.activeReportsChange = this.activeReportsChange.bind(this);
    this.handleToggleSubmissions = this.handleToggleSubmissions.bind(this);
    this.handleTitleUpdate = this.handleTitleUpdate.bind(this);
    this.handleImageUpdate = this.handleImageUpdate.bind(this);
    this.routerWillLeave = this.routerWillLeave.bind(this);

    this.state = {
      storyId: props.params.id,
      writeAssistOn: false,
      isClassOptionsModified: false,
      isGenreOptionsModified: false,
      isAssignmentOptionsModified: false,
      isMiscOptionsModified: false,
      classOptions: [],
      genreOptions: [],
      assignmentOptions: [],
      miscOptions: [],
      characterLimit: 400,
      isCharacterLimitModified: false,
      activeReports: ['grammar', 'dva', 'slength', 'passive', 'consistency'],
      isEditings: [],
      editingHistoryPages: [],
      isShowingSubmissions: false,
    };
  }

  observe(props) {
    var objectId = props.params.id;
    var story = new Parse.Object('Story');
    story.id = props.params.id;
    const storyQuery = new Parse.Query('Story').equalTo('objectId', objectId);
    const pageQuery = new Parse.Query('Page').equalTo('story', story).equalTo('statusRaw', 1).include('creator');
    const submissionsQuery = new Parse.Query('Page')
      .equalTo('story', story)
      .equalTo('statusRaw', PageStatusConstants.PENDING)
      .equalTo('creator', Parse.User.current());
    const aidStoryQuery = new Parse.Query('Aid').matchesQuery('story', storyQuery);
    const aidPageQuery = new Parse.Query('Aid').matchesQuery('page', pageQuery);
    const aidQuery = Parse.Query.or(aidStoryQuery, aidPageQuery);
    return {
      stories: storyQuery.include('creator').include('approver'),
      pages: pageQuery,
      submissions: submissionsQuery,
      likes: new Parse.Query('Like').equalTo('story', story).equalTo('creator', Parse.User.current()).equalTo('typeRaw', 0).include('story').include('page'),
      aids: aidQuery,
      labels: new Parse.Query('Label'),
      user: ParseReact.currentUser,
    };
  }

  componentWillMount() {
    super.componentWillMount();
    this.sub = pubnub({
      subscribe_key: secrets.pubnubSub
    });

    this.sub.subscribe({
      channel: `story-${this.props.params.id}`,
      message: (message) => {
        if (!_.includes(message.excludedUserIds, this.data.user.objectId)) {
          this.refresh();
        } else {
          console.log('no need to refresh');
        }
      }
    });
  }

  componentDidMount() {
    this.context.router.setRouteLeaveHook(this.props.route, this.routerWillLeave);
  }

  componentWillUnmount() {
    super.componentWillUnmount();
    this.sub.unsubscribe({
      channel: `story-${this.props.storyId}`
    });
  }

  routerWillLeave(nextLocation) {
    if (!this.data.stories[0].title.length) {
      this.refs.notificationSystem.addNotification({
        message: 'Your story must have a title!',
        level: 'error'
      });
      return false;
    };
  }

  getData(state) {
    let classOptions = state.classOptions;
    let genreOptions = state.genreOptions;
    let assignmentOptions = state.assignmentOptions;
    let miscOptions = state.miscOptions;

    if (this.data.stories && this.data.stories[0]) {
      const story = this.data.stories[0];
      const ids = story.labels && story.labels.map((label) => label && label.objectId);
      const savedLabels = _(this.data.labels)
        .filter((label) => {
          return ids && ids.indexOf(label.objectId) !== -1;
        })
        .groupBy('typeRaw')
        .value();
      if (!state.isClassOptionsModified) {
        classOptions = savedLabels[LabelTypeConstants.CLASS] || [];
      };
      if (!state.isGenreOptionsModified) {
        genreOptions = savedLabels[LabelTypeConstants.GENRE] || [];
      };
      if (!state.isAssignmentOptionsModified) {
        assignmentOptions = savedLabels[LabelTypeConstants.ASSIGNMENT] || [];
      };
      if (!state.isMiscOptionsModified) {
        miscOptions = savedLabels[LabelTypeConstants.MISC] || [];
      };
    };

    return {
      selectedLabels: classOptions.concat(genreOptions).concat(assignmentOptions).concat(miscOptions)
    };
  }

  refresh() {
    this.refreshQueries(['stories', 'pages', 'aids']);
  }

  handleToggleSubmissions(value) {
    this.setState({isShowingSubmissions: value});
  }

  handleClickWriteAssist(e) {
    this.setState({
      writeAssistOn: !this.state.writeAssistOn
    });

    Analytics.track('Story', 'Clio', this.state.writeAssistOn ? 'On' : 'Off', {
      objectId: this.state.storyId,
      storyId: this.state.storyId,
      writeAssistOn: !this.state.writeAssistOn
    });
  }

  handleShowCommentBox(ref) {
    this.refs['CommentBox'].hideCommentBox();
    this.data.pages.forEach(p => {
      let currentRef = 'CommentBox_' + p.id.objectId;
      if (ref != currentRef) {
        this.refs[currentRef].hideCommentBox();
      }
    });
  }

  handleSubmit() {
    if (this.state.editingPage) {
      this.setState({editingPage: null});
    }
    this.refs.submissions && this.refs.submissions.showLastSlide();
  }

  handleLabelChange(type, value) {
    const state = {
      classOptions: type === LabelTypeConstants.CLASS ? value : this.state.classOptions,
      genreOptions: type === LabelTypeConstants.GENRE ? value : this.state.genreOptions,
      assignmentOptions: type === LabelTypeConstants.ASSIGNMENT ? value : this.state.assignmentOptions,
      miscOptions: type === LabelTypeConstants.MISC ? value : this.state.miscOptions,
      isClassOptionsModified: type === LabelTypeConstants.CLASS || this.state.isClassOptionsModified,
      isGenreOptionsModified: type === LabelTypeConstants.GENRE || this.state.isGenreOptionsModified,
      isAssignmentOptionsModified: type === LabelTypeConstants.ASSIGNMENT || this.state.isAssignmentOptionsModified,
      isMiscOptionsModified: type === LabelTypeConstants.MISC || this.state.isMiscOptionsModified,
    };
    this.setState(state);

    ParseReact.Mutation.Set(this.data.stories[0], {
      labels: this.getData(state).selectedLabels
    }).dispatch();
  }

  handleCharacterLimitChange(e) {
    const value = parseInt(e.target.value);
    this.setState({characterLimit: value, isCharacterLimitModified: true});
    ParseReact.Mutation.Set(this.data.stories[0], {
      characterLimit: value
    }).dispatch();
  }

  handleStartEdit(object) {
    const { isEditings } = this.state;

    if (this.data.user.typeRaw === UserTypeConstants.STUDENT) {
      this.setState({isEditings: [...isEditings, object.objectId]});
    }
  }

  handleCancelEdit(object) {
    const { isEditings } = this.state;
    if (this.data.user.typeRaw === UserTypeConstants.STUDENT) {
      this.setState({
        isEditings: isEditings.filter(editing => editing !== object.objectId),
      });
    }
  }

  handleContentUpdate(object, value) {
    const { isEditings } = this.state;
    const newEditings = isEditings.filter(editing => editing !== object.objectId);
    this.setState({isEditings: newEditings});
    let mutation = Object.assign({}, value);
    const batch = new ParseReact.Mutation.Batch();
    if (this.data.user.typeRaw === UserTypeConstants.STUDENT) {
      mutation.edited = true;
      mutation.morePageCount = object.morePageCount ? object.morePageCount + 1 : 1;
      ParseReact.Mutation.Create('Page', {
        content: object.content,
        refPageId: object.objectId,
        creator: object.creator,
        story: object.className === 'Story' ? object : object.story,
        likeCount: object.likeCount || 0,
        statusRaw: PageStatusConstants.HISTORY
      }).dispatch({batch});
    }
    ParseReact.Mutation.Set(object, mutation).dispatch({batch});
    batch.dispatch();
  }

  handlePendingEdit(page) {
    this.setState({editingPage: page});
  }

  handleTitleUpdate(title) {
    const story = this.data.stories[0];
    ParseReact.Mutation.Set(story, {
      title
    }).dispatch();
  }

  handleImageUpdate(files) {
    const image = new Parse.File('file', files[0]);
    const story = this.data.stories[0];
    image.save().then(parseImage => {
      return ParseReact.Mutation.Set(story, {
        image: parseImage
      }).dispatch();
    }, error => {
      console.log('error saving image', error);
    });
  }

  renderStoryHeader() {
    const { writeAssistOn, activeReports, isEditings} = this.state;
    return (
      <div className="story-container grid-block">
        {this.data.stories.map(function(s) {
          this.data.storyApprover = s.approver;

          let likedStories = this.data.likes.filter((like) => {
            return like.page == undefined
                    && like.story.id.objectId === s.id.objectId;
          });

          let ref = 'CommentBox';

          const aids = this.data.aids &&
            this.data.aids.filter((a) => a.story && a.story.objectId === s.objectId);

          this.data.storyHeaderPage = (
            <div className="grid-container print">
              <PageRow
                {...s}
                key={s.id.objectId}
                ref={ref}
                isLiked={likedStories.length > 0}
                onShowCommentsBox={this.handleShowCommentBox.bind(this, ref)}
                writeAssistOn={writeAssistOn}
                activeReports={activeReports}
                currentUser={this.data.user}
                isEditing={isEditings.indexOf(s.objectId) !== -1}
                aids={aids}
                allowEdit={this.data.user.typeRaw === UserTypeConstants.TEACHER}
                onStartEdit={this.handleStartEdit.bind(this, s)}
                onCancelEdit={this.handleCancelEdit.bind(this, s)}
                onContentUpdate={this.handleContentUpdate.bind(this, s)}
              />
            </div>
          );
          if (this.data.user && this.data.user.typeRaw === UserTypeConstants.TEACHER) {
            return (
              <EditableStoryHeader
                title={s.title}
                imageUrl={s.image._url}
                onChange={this.handleTitleUpdate}
                onDrop={this.handleImageUpdate}
                newStory={'No'}
              />
            );
          } else {
            return (
              <StoryHeader
                key="story-header"
                {...s}
              />
            );
          }

        }, this)}
      </div>
    );
  }

  renderCharacterLimit() {
    const { isCharacterLimitModified, characterLimit } = this.state;
    if (this.data.stories.length) {
      const story = this.data.stories[0];
      const value = isCharacterLimitModified ? characterLimit : story.characterLimit;
      return (
        <div className="character-limit grid-container">
          <div className="grid-block">
            <div className="small-6 grid-content v-align">
              Character Count:
            </div>
            <div className="small-18 grid-content">
              {this.data.user.typeRaw === UserTypeConstants.TEACHER &&
                <input
                  type="number"
                  step="10"
                  min="0"
                  value={value}
                  onChange={this.handleCharacterLimitChange}
                />
              }
              {this.data.user.typeRaw === UserTypeConstants.STUDENT &&
                value
              }
            </div>
          </div>
        </div>
      );
    }
  }

  renderStoryHeaderPage() {
    return this.data.storyHeaderPage;
  }

  renderPagesContent() {
    const { isEditings } = this.state;
    return (
      <div>
        {this.data.pages.map((p) => {
          let ref = 'CommentBox_' + p.id.objectId;

          const likedPages = this.data.likes &&
            this.data.likes.filter((like) => {
              return like.page !== undefined
                      && like.page.id.objectId === p.id.objectId;
            });

          const aids = this.data.aids &&
            this.data.aids.filter((a) => a.page && a.page.objectId === p.objectId);
          return (
            <div className="grid-container" key={p.id.objectId}>
              <PageRow
                {...p}
                ref={ref}
                onShowCommentsBox={this.handleShowCommentBox.bind(this, ref)}
                isLiked={likedPages.length > 0}
                writeAssistOn={this.state.writeAssistOn}
                activeReports={this.state.activeReports}
                likes={this.data.likes}
                isEditing={isEditings.indexOf(p.objectId) !== -1}
                aids={aids}
                onStartEdit={this.handleStartEdit.bind(this, p)}
                onCancelEdit={this.handleCancelEdit.bind(this, p)}
                currentUser={this.data.user}
                allowEdit={
                  this.data.user.typeRaw === UserTypeConstants.TEACHER && p.statusRaw === PageStatusConstants.APPROVED
                }
                onContentUpdate={this.handleContentUpdate.bind(this, p)}
              />
            </div>
          );
        })}
      </div>
    );
  }

  renderSubmissions(lastPage) {
    let pendingSession, interactiveSection, characterLimit;

    const { stories, storyApprover, user, submissions } = this.data;

    const approveSlider = (
      <ApproveSlider
        {...this.state}
        likes={this.data.likes}
        writeAssistOn={this.state.writeAssistOn}
        activeReports={this.state.activeReports}

        refresh={this.refresh.bind(this)}
        refPageId={lastPage.id.objectId}
      />
    );

    if (stories.length) {
      characterLimit = this.state.isCharacterLimitModified ? this.state.characterLimit : this.data.stories[0].characterLimit;
    } else {
      characterLimit = this.state.characterLimit;
    }

    let hideNewSubmission = false;
    const newSubmission = (hide) => (
      <div className={classNames('grid-container', {hide})}>
        <NewSubmission
          {...this.state}
          refPageId={lastPage.id.objectId}
          finishSubmit={this.handleSubmit}
          route={this.props.route}
          characterLimit={characterLimit}
        />
      </div>
    );

    if (user.typeRaw === UserTypeConstants.TEACHER) {
      interactiveSection = (
        <div>
          {approveSlider}
          {newSubmission(hideNewSubmission)}
        </div>
      );
      return [pendingSession, interactiveSection];
    } else {
      if (storyApprover && storyApprover.id.objectId === user.id.objectId) {
        pendingSession = '';
        interactiveSection = approveSlider;
      } else {
        pendingSession = (
          <Submissions
            {...this.state}
            ref="submissions"
            onToggle={this.handleToggleSubmissions}
            likes={this.data.likes}
            refPageId={lastPage.id.objectId}
            onEdit={this.handlePendingEdit}
            activeReports={this.state.activeReports}
          />
        );

        const lastSubmission = submissions.filter(p => {
          return p.refPageId === lastPage.objectId
            && p.creator.objectId === user.objectId;
        });

        hideNewSubmission = lastSubmission.length > 0;
        interactiveSection = newSubmission(hideNewSubmission);
      }
    }



    return [pendingSession, interactiveSection];
  }

  activeReportsChange(reports) {
    this.setState({
      activeReports: reports
    });
  }

  render() {

    let headerContent, pagesContent, pendingSession, interactiveSection, selectedLabels;

    if (this.pendingQueries().length) {
      headerContent = <div className="loading grid-frame">
                        <div className="spinner">
                          <div className="bounce1"></div>
                          <div className="bounce2"></div>
                          <div className="bounce3"></div>
                        </div>
                      </div>;
    } else if (this.data.stories.length) {
      headerContent = (
        <div className="small-24 medium-24 large-24 grid-block">
          {this.renderStoryHeader()}
        </div>
      );

      let lastPage = this.data.stories[0];
      if (this.data.pages.length) {
        let pLength = this.data.pages.length;
        lastPage = this.data.pages[pLength - 1];
      }

      if (this.data.pages && this.data.likes) {
        pagesContent = this.renderPagesContent();
      }

      [pendingSession, interactiveSection] = this.renderSubmissions(lastPage);
    } else {
      headerContent = <div className="grid-frame">No Stories</div>;
    }

    const data = this.getData(this.state);
    return (
      <section className="story vertical">
        <WriteAssistButton isOn={this.state.writeAssistOn} onClick={this.handleClickWriteAssist}/>

        <EnglishReports
          onChange={this.activeReportsChange}
        />

        {headerContent}

        {this.renderCharacterLimit()}

        <div className="grid-container">
          <Labels
            selectedLabels={data.selectedLabels}
            availableLabels={this.data.labels}
            manageLabels={this.data.user && this.data.user.typeRaw === UserTypeConstants.TEACHER}
            onLabelChange={this.handleLabelChange}
          />
        </div>

        {this.renderStoryHeaderPage()}

        {pagesContent}

        {pendingSession}

        {interactiveSection}

        <NotificationSystem ref="notificationSystem" />
      </section>
    );
  }
};

Story.defaultProps = {
  stories: [],
  pages: [],
  likes: []
};

Story.contextTypes = contextTypes;




