import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';
import ParseAuth from '../../support/ParseAuth';
import MenuItem from './MenuItem';
import UserProfile from '../../support/UserProfile';
import Analytics from '../../support/Analytics';

export default class Dropdown extends ParseComponent {

  constructor(props) {
    super(props);
    this.state = {
      isShowing: false,
    };

    this.toggleShow = this.toggleShow.bind(this);
    this.toggleHide = this.toggleHide.bind(this);
  }

  observe(props) {
    return {
      user: ParseReact.currentUser
    };
  }

  componentWillUnmount() {
    super.componentWillUnmount();
    document.removeEventListener('click', this.toggleHide);
  }

  toggleShow() {
    this.setState({isShowing: true});
    document.addEventListener('click', this.toggleHide);

    let label = this.props.avatar ? 'profile' : 'admin';
    Analytics.track('User', 'Dropdown', label);
  }

  toggleHide() {
    this.setState({isShowing: false});
    document.removeEventListener('click', this.toggleHide);
  }

  render() {
    return (
      <div>
        <div className="drop-down-menu" onClick={this.toggleShow}>
          <img className={this.props.avatar && 'avatar'} src={this.props.iconUrl} />
          <ul className={classNames('sub-menu', 'text-left', {hide: !this.state.isShowing})}>
            {this.props.children}
          </ul>
        </div>
      </div>
    );
  }

}

Dropdown.defaultProps = {
  user: {}
};



