import React from 'react';
import { render } from 'react-dom';
import TitleBarCenter from './TitleBarCenter';
import TitleBarRight from './TitleBarRight';
import TitleBarLeft from './TitleBarLeft';
import classNames from 'classnames';

const TitleBar = class TitleBar extends React.Component {
  render() {
    return (
      <div className={classNames('title-bar', this.props.className)}>
        {this.props.children}
      </div>
    );
  }
};

TitleBar.Center = TitleBarCenter;
TitleBar.Right = TitleBarRight;
TitleBar.Left = TitleBarLeft;

export default TitleBar;



