import React from 'react';
import classNames from 'classnames';
import UserPanelTable from './UserPanelTable';
import _ from 'lodash';
import { Parse } from 'parse';
import { Link } from 'react-router';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import LabelTypeConstants from '../../constants/LabelTypeConstants';
import UserTypeConstants from '../../constants/UserTypeConstants';
import UserStatusConstants from '../../constants/UserStatusConstants';
import UserProfile from '../../support/UserProfile';
import NotificationSystem from 'react-notification-system';
import SpinnerButton from '../widgets/SpinnerButton';
import Dropzone from 'react-dropzone';
import Baby from 'babyparse'; 

export default class UserPanel extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      selectedType: UserTypeConstants.STUDENT,
      selectedRows: [],
      selectedAll: false,
      isEditing: false,
      isSaving: false,
      warnedDelete: false,
      isDeleting: false,
      changes: [],
      maxUserNumber: 50
    };

    this.rowGetter = this.rowGetter.bind(this);
    this.handleClickSave = this.handleClickSave.bind(this);
    this.handleClickEdit = this.handleClickEdit.bind(this);
    this.handleClickCancel = this.handleClickCancel.bind(this);
    this.handleClickDelete = this.handleClickDelete.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
    this.handleChangeInput = this.handleChangeInput.bind(this);
    this.handleChangeSelectedRows = this.handleChangeSelectedRows.bind(this);
  }

  observe(props, state) {
    return {
      users: new Parse.Query(Parse.User)
        .equalTo('typeRaw', state.selectedType)
        .notEqualTo('statusRaw', UserStatusConstants.DELETED)
        .ascending('username')
        .limit(1000),
      classes: new Parse.Query('Label')
        .equalTo('typeRaw', LabelTypeConstants.CLASS),
    };
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
    Parse.Config.get().then((options) => {
      this.setState({maxUserNumber: options.attributes.maxUserNumber});
    });
  }

  rowGetter(index) {
    const user = this.data.users[index];
    if (!this.state.isEditing) {
      return [
        user.email,
        user.firstName,
        user.lastName,
        user.username,
        user.classLabel && user.classLabel.objectId,
        user.statusRaw,
        user.objectId,
      ];
    } else {
      const change = this.state.changes[index];
      return [
        (change && change.email !== undefined) ? change.email : user.email,
        (change && change.firstName !== undefined) ? change.firstName : user.firstName,
        (change && change.lastName !== undefined) ? change.lastName : user.lastName,
        (change && change.username !== undefined) ? change.username : user.username,
        (change && change.classLabel !== undefined) ? change.classLabel : (user.classLabel && user.classLabel.objectId),
        (change && change.statusRaw !== undefined) ? change.statusRaw : user.statusRaw,
        user.objectId,
      ];
    };
  }

  handleClickType(type) {
    this.setState({selectedType: type});
  }

  handleChangeSelectedRows(rows) {
    this.setState({selectedRows: rows});
  }

  handleClickEdit() {
    this.setState({isEditing: true});
  }

  handleClickCancel() {
    this.setState({
      isEditing: false,
      isSaving: false,
      isDeleting: false,
      warnedDelete: false,
      changes: []
    });
  }

  handleClickDelete() {
    if (!this.state.warnedDelete) {
      this._notificationSystem.addNotification({
        message: 'Are you sure you want to delete selected users?',
        level: 'error',
        position: 'tc',
      });
    } else {
      this.setState({isDeleting: true});
      const users = this.data.users.filter((user, index) => {
        return this.state.selectedRows.indexOf(index) !== -1;
      });
      const data = users.map((user) => {
        return {
          objectId: user.objectId,
          changes: {statusRaw: UserStatusConstants.DELETED}
        };
      });
      Parse.Cloud.run('updateUsers', {data}).then((result) => {
        this._notificationSystem.addNotification({
          message: 'Deleted users',
          level: 'success',
        });
        this.refreshQueries();
        this.setState({isDeleting: false, isEditing: false,});
      }, (error) => {
        this._notificationSystem.addNotification({
          message: 'Error deleting users',
          level: 'error',
        });
        this.setState({isDeleting: false, isEditing: false,});
      });
    };
    this.setState({warnedDelete: !this.state.warnedDelete});
  }

  handleClickSave() {
    const data = _.chain(this.state.changes)
      .map((change, index) => {
        if (this.state.selectedRows.indexOf(index) !== -1 && change) {
          return {objectId: this.data.users[index].objectId, changes: change};
        } else {
          return null;
        };
      })
      .compact()
      .value();

    this.setState({isSaving: true});

    Parse.Cloud.run('updateUsers', {data}).then((result) => {
      this._notificationSystem.addNotification({
        message: 'Updated users',
        level: 'success',
      });
      this.refreshQueries();
      this.setState({isSaving: false, isEditing: false, changes: []});
    }, (error) => {
      this._notificationSystem.addNotification({
        message: 'Error saving changes',
        level: 'error',
      });
      this.setState({isSaving: false, isEditing: false, changes: []});
    });
  }

  handleChangeInput(index, type, e) {
    let changes = this.state.changes;
    let change = this.state.changes[index] || {};
    const value = e.target.value;
    switch (type) {
      case 'statusRaw':
        change[type] = parseInt(value);
        break;
      default:
        change[type] = value;
    };
    changes[index] = change;
    this.setState({changes:changes});
  }

  uploadFile() {
    
    const csv = this.refs.file.files[0];
    const reader = new FileReader();
    
    reader.onload = () => {
    
      var csvParsed = Baby.parse(reader.result, {
        header: true
      });

      //check the csv is right format
      if (csvParsed.meta.fields[0] === 'UserName' &&
          csvParsed.meta.fields[1] === 'Password' &&
          csvParsed.meta.fields[2] === 'FirstName' &&
          csvParsed.meta.fields[3] === 'LastName' &&
          csvParsed.meta.fields[4] === 'Class' &&
          csvParsed.meta.fields.length === 5) {
        
        //check that new users are within max user limit
        if (csvParsed.data.length + this.data.users.length <= this.state.maxUserNumber) {
          
          this._notificationSystem.addNotification({
            message: 'Processing CSV file.  Please refresh in a minute',
            level: 'success'
          });

          //run parse cloud to add users
          Parse.Cloud.run('parseUserCSV', {csv: csvParsed}).then((result) => {
            console.log('result from parse cloud function', result);
          }, (error) => {
            console.log('error occurred running cloud function', error);
            });
          } else {
              this._notificationSystem.addNotification({
                message: 'You are trying to add user exceeding max user limit.  Please contact StoryAppy Admin to increase user limit',
                level: 'error'
              });
          }

      } else {
          this._notificationSystem.addNotification({
            message: 'Fields don\'t match',
            level: 'error'
          });
      }      
    };

      this.refs.file.value = null;
      reader.readAsBinaryString(csv);
  }

  render() {
    let editButton, addButton, deleteButton, saveButton, cancelButton, uploadButton;
    if (this.state.isEditing) {
      deleteButton = (
        <SpinnerButton
          className={classNames(
            'small-4',
            'medium-4',
            'button',
            'hollow',
            'alert',
            'rounded',
            'delete',
            {disabled: this.state.isSaving}
          )}
          spinnerColorClass="alert"
          onClick={this.handleClickDelete}
          isSpinning={this.state.isDeleting}
        >
          {this.state.warnedDelete ? 'Confirm Delete' : 'Delete Account'}
        </SpinnerButton>
      );
      saveButton = (
        <SpinnerButton
          className={classNames(
            'small-3',
            'medium-3',
            'button',
            'hollow',
            'rounded',
            'save',
            {disabled: this.state.isDeleting}
            )}
          onClick={this.handleClickSave}
          isSpinning={this.state.isSaving}
        >
          Save
        </SpinnerButton>
      );
      cancelButton = (
        <div className="grid-block shrink">
          <div
            className="button hollow rounded cancel secondary"
            onClick={this.handleClickCancel}
          >
            Cancel
          </div>
        </div>
      );
    } else {
      if (this.data.users.length && this.data.users.length < this.state.maxUserNumber) {
        addButton = (
          <div className="grid-block shrink">
              <Link
                className="button hollow rounded success add"
                to="/admin/users/new"
              >New Account</Link>
          </div>
        );
      }

      editButton = (
        <div className="grid-block">
          <div
            className="button hollow rounded edit"
            onClick={this.handleClickEdit}
          >
            Edit
          </div>
        </div>
      );

      if (this.state.selectedType === UserTypeConstants.STUDENT) {
        uploadButton = (
          <div className="grid-block">
            <div className="button hollow upload rounded">
              <span>Upload CSV</span>   
              <input type="file" name="file" ref="file" onChange={this.uploadFile}/>   
              <div className="topLabelHover button">Import STUDENTS. Format: UserName, Password, FirstName, LastName, Class</div>
            </div>
          </div>
        );  
      }
    };

    return (
      <div className="user-panel grid-block vertical">
        <div className="grid-container">
          <div className="grid-block">
            <div className="button-group segmented rounded">
              <li
                className={classNames({
                  'is-active': this.state.selectedType === UserTypeConstants.STUDENT,
                })}
              >
                <a
                  onClick={this.handleClickType.bind(this, UserTypeConstants.STUDENT)}
                >Students</a>
              </li>
              <li
                className={classNames({
                  'is-active': this.state.selectedType === UserTypeConstants.TEACHER,
                })}
              >
                <a
                  onClick={this.handleClickType.bind(this, UserTypeConstants.TEACHER)}
                >Teachers</a>
              </li>
            </div>
          </div>
          <div className="grid-block">
            <div className="grid-block shrink">=
              <p className="count v-align">{`${this.state.selectedType === UserTypeConstants.STUDENT ? 'Students' : 'Teachers'} (${this.data.users.length} of ${this.state.maxUserNumber})`}</p>
            </div>
            {addButton}
            {deleteButton}
            {editButton}
            {uploadButton}
            {cancelButton}
            {saveButton}
          </div>
          <div className="grid-block table">
            <UserPanelTable
              onChangeSelectedRows={this.handleChangeSelectedRows}
              rowGetter={this.rowGetter}
              rowLength={this.data.users.length}
              isEditing={this.state.isEditing}
              classes={this.data.classes}
              onChangeInput={this.handleChangeInput}
            />
          </div>
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </div>
    );
  }
};



