import React from 'react';
import _ from 'lodash';

export default class EnglishReports extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      grammar: true,
      dva:true,
      slength:true,
      passive:true,
      consistency:true
    };

    this.activeReportChange = this.activeReportChange.bind(this);
  }

  activeReportChange(report) {
    const newState = Object.assign({}, this.state);
    newState[report] = !this.state[report];
  
    const reports = _.transform(newState, (result, value, key) => {
      if (value) result.push(key);
    }, []);
    
    this.props.onChange(reports);
    this.setState(newState);
  }

  render() {
    return (
      <div className="aid-category grid-container">
        <div className="grid-block">
          <div className="grid-block">
            <div className="grammar tile">
              <input type="checkbox" className="checkbox" defaultChecked onChange={() => this.activeReportChange('grammar')} />
            </div>
            <div className="grid-block v-align"> GRAMMAR</div>
          </div>
          <div className="grid-block">
            <div className="dva tile"> 
              <input type="checkbox" className="checkbox" defaultChecked onChange={() => this.activeReportChange('dva')}/>
            </div>
            <div className="grid-block v-align"> DICTION</div>
          </div>
          <div className="grid-block">
            <div className="slength tile">
              <input type="checkbox" className="checkbox" defaultChecked onChange={() => this.activeReportChange('slength')}/>
            </div>
            <div className="grid-block v-align"> LENGTH</div>
          </div>
          <div className="grid-block">
            <div className="passive tile">
              <input type="checkbox" className="checkbox" defaultChecked onChange={() => this.activeReportChange('passive')}/>
            </div>
            <div className="grid-block v-align"> STYLE</div>
          </div>
          <div className="grid-block">
            <div className="consistency tile">
              <input type="checkbox" className="checkbox" defaultChecked onChange={() => this.activeReportChange('consistency')}/>
            </div>
            <div className="grid-block v-align"> CONSISTENCY</div>
          </div>
        </div>
      </div>
    );
  }
}


