import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import classNames from 'classnames';

import Analytics from '../../support/Analytics';
import Page from './Page';

export default class ApprovePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isSaved: this.props.isSaved,
    };
  }

  shouldComponentUpdate(nextProps, nextState) {
    return nextProps.id.objectId != this.props.id.objectId
          || nextState != this.state
          || nextProps.content !== this.props.content
          || nextProps.writeAssistOn !== this.props.writeAssistOn
          || nextProps.allowStudentEdit !== this.props.allowStudentEdit
          || nextProps.currentUser !== this.props.currentUser
          || nextProps.innerHTML !== this.props.innerHTML
          || nextProps.activeReports !== this.props.activeReports;
  }

  handleOnSaveClick() {
    var newVal = !this.state.isSaved;
    this.setState({
      isSaved: newVal
    });
    this.props.onSaveClick(newVal);

    Analytics.track('Page', 'ApproveFilterStar', 'Selected: ' + !this.state.isSaved, {
      objectId: this.props.objectId,
      pageId: this.props.objectId
    });
  }

  handleOnApproveClick() {
    ParseReact.Mutation.Set(this.props, {
      statusRaw: 1
    }).dispatch().then(() => {
      Parse.Cloud.run('approvePage', {pageId: this.props.objectId}, {
        success: (result) => {
          this.props.refreshStory();

          Analytics.track('Page', 'ApprovePage', 'Success', {
            objectId: this.props.objectId,
            pageId: this.props.objectId
          });
        },
        error: (e) => {
          Analytics.track('Page', 'ApprovePage', 'Error', e);
        }
      });
    });
  }

  render() {
    // var starIconClass = classNames({
    //   'fa fa-star': true,
    //   'star-icon': true,
    //   'active': this.state.isSaved
    // });

    return (
      <div className="page-actions-row">
        <Page {...this.props} />

        <div className="footer-buttons">
          <div className="approve-button info button hollow" onClick={this.handleOnApproveClick.bind(this)}>
            Approve
          </div> 
        </div>
      </div>
    );
  }
}

ApprovePage.propTypes = {
  onSaveClick: React.PropTypes.func.isRequired
};

ApprovePage.defaultProps = { isSaved: false };



