import DropElement from '../components/tether/DropElement';
import React from 'react';
import S from 'string';
import $ from 'jquery';
import Hint from '../components/pages/Hint';
import format from './HtmlFormatter';

function Hintify (node, content, tags, editable = false, activeReports, target) {
  this.node = node;
  this.content = content;
  this.editable = editable;
  this.tags = tags;
  this.drops = [];
  this.activeReports = activeReports;
  this.target = target;

  if (tags) {
    this.setTags(tags);
  };
}

Hintify.prototype.setTags = function (tags) {

  const { activeReports } = this;

  const endPos = tags.filter((tag) => {
    if (activeReports) {
      return activeReports.indexOf(tag.report) !== -1;

    }
  }).map((tag) => {
    return {pos: tag.endPos, tag: '</span>', type: 'end'};
  });

  const startPos = tags.filter((tag) => {
      if (activeReports) {
        return activeReports.indexOf(tag.report) !== -1;
      }
  }).map((tag) => {
    return {
      pos: tag.startPos,
      endPos: tag.endPos,
      type: 'start',
      tag: `<span contenteditable='${this.editable}' class='aid' data-category='${tag.categoryDisplayName}' data-report='${tag.report}' data-hint='${tag.hint && S(tag.hint).escapeHTML().s.replace(/,/g, ', ')}'>`
    };
  });

  // const endPos = tags.map((tag) => {
  //   console.log('Tags', tags);
  //   return {pos: tag.endPos, tag: '</span>', type: 'end'};
  // });

  // const startPos = tags.map((tag) => {
  //   return {
  //     pos: tag.startPos,
  //     endPos: tag.endPos,
  //     type: 'start',
  //     tag: `<span contenteditable='${this.editable}' class='aid' data-category='${tag.categoryDisplayName}' data-report='${tag.report}' data-hint='${tag.hint && S(tag.hint).escapeHTML().s.replace(/,/g, ', ')}'>`
  //   };
  // });

  const elements = [endPos, startPos].reduce((a,b) => {
    return a.concat(b);
  }, []).sort((a, b) => {
    if (a.pos > b.pos) {
      return 1;
    } else if (a.pos === b.pos) {
      if (a.type === 'start' && b.type === 'end') {
        return -1;
      } else if (a.type === 'start' && b.type === 'start') {
        if (a.endPos < b.endPos) {
          return 1;
        } else {
          return -1;
        };
      } else {
        return 1;
      };
    } else {
      return -1;
    }
    return 0;
  });

  const htmlContent = elements
    .reduceRight((sum, element, i) => {
    let start, end;
    if (element.type === 'start') {
      end = sum.slice(element.pos);
      start = sum.slice(0, element.pos);
    } else {
      end = sum.slice(element.pos + 1);
      start = sum.slice(0, element.pos + 1);
    };

    return start + element.tag + end;
  }, this.content);

  this.htmlContent = format(htmlContent);
};

Hintify.prototype.setDrops = function () {
  $(this.node).find(this.target).each((index, span) => {
    const $span = $(span);

    $span.addClass($span.data('report'));

    let drop = new DropElement({
      target: span,
      position: 'bottom right',
    });

    drop.setElement(
      <div className="hint">
        <Hint
          category={$span.data('category')}
          hint={$span.data('hint') && S($span.data('hint')).unescapeHTML().s}
          report={$span.data('report')}
          onClose={drop.close.bind(drop)}
        />
      </div>
    );

    this.drops.push(drop);
  });
};

Hintify.prototype.showHint = function () {
  if (!this.drops.length) {
    this.setDrops();
  };
};

Hintify.prototype.destroy = function () {
  if (this.drops) {
    for (var i = 0; i < this.drops.length; i++) {
      this.drops[i].destroy();
    };
  };
  this.drops = [];
};

Hintify.prototype.hideHint = function () {
  $(this.node).find('span.aid').each((index, span) => {
    const $span = $(span);
    $span.removeClass($span.data('report'));
  });
  this.destroy();
};

module.exports = Hintify;


