import React from 'react';
import { Link } from 'react-router';
import moment from 'moment';

import UserProfile from '../../support/UserProfile';
import Avatar from '../users/Avatar';
import ActivityStatusConstants from '../../constants/ActivityStatusConstants';

export default class Notification extends React.Component {
  renderContent() {
    let fromUserName = UserProfile.getDisplayName(this.props.fromUser);
    let storyTitle = this.props.story.title;

    switch (this.props.typeRaw) {
      case ActivityStatusConstants.APPROVE:
        return fromUserName + ' approved your submission for ' + storyTitle;
      case ActivityStatusConstants.DISAPPROVE:
        return 'Your submission to ' + storyTitle + ' was not accepted. Don\'t give up! Try again!';
      case ActivityStatusConstants.BOOKMARK:
        return fromUserName + ' has bookmarked ' + storyTitle;
      case ActivityStatusConstants.CONTRIBUTE:
        return fromUserName + ' has contributed to ' + storyTitle;
      case ActivityStatusConstants.CREATE:
        return fromUserName + ' has created a new story ' + storyTitle;
      case ActivityStatusConstants.FRIEND:
        return 'You\'re now friends with ' + fromUserName;
      case ActivityStatusConstants.JOIN:
        return 'Your Facebook friend ' + fromUserName + ' joined StoryAppy.';
      case ActivityStatusConstants.RATE:
        return fromUserName + ' liked the story ' + storyTitle;
      case ActivityStatusConstants.STORY_MEMBER:
        return fromUserName + ' wants you to contribute to ' + storyTitle;
      case ActivityStatusConstants.FOLLOW:
        return fromUserName + ' has followed You';
      case ActivityStatusConstants.STORY_UPDATE:
        return storyTitle + ' is updated';
      case ActivityStatusConstants.COMMENT:
        return fromUserName + ' commented on your contribution to ' + storyTitle;
      case ActivityStatusConstants.PENDING_SUBMISSION:
        let content = 'Oh, you haven\'t approved ' + storyTitle;
        switch (this.props.statusRaw) {
          case 0: return content + '\'s submissions for 12 hours';
          case 1: return content + '\'s submissions for a day';
          case 2: return content + '\'s submissions for 2 days. You will no longer be the approver if you don\'t pick one submission to approve for 4 days';
          case 3: return content + '\'s submissions for 3 days';
          default: break;
        }
      case ActivityStatusConstants.APPROVE_CHANGE:
        switch (this.props.statusRaw) {
          case 0: return 'Sorry, ' + storyTitle + '\'s approval is taking too long. Approval is now given to the story\'s creator';
          case 1: return storyTitle + '\'s approval is taking too long. You have become the approver';
          default: break;
        }
      case ActivityStatusConstants.ENABLE_EDIT:
        return `You're allowed to edit your submission in ${storyTitle}`;
      case ActivityStatusConstants.EDITED:
        return `${fromUserName} has edited a submission in ${storyTitle}`;
    }
    return 'Opps...something went wrong';
  }

  renderSince() {
    let end = moment(this.props.createdAt);
    return end.from(moment());
  }

  render() {
    let creator = this.props.fromUser;

    return (
      <li className="menu-item activity-item">
        <Link to={`/stories/${this.props.story.objectId}`}>
          <div className="vertical grid-block">
            <div className="grid-block">
              <div className="small-4 grid-block align-right">
                <img className="avatar" src={UserProfile.getSmallAvatar(creator)} title={UserProfile.getDisplayName(creator)}/>
              </div>
              <div className="small-20 grid-content">
                {this.renderContent()}
              </div>
            </div>
            <div className="grid-block">
              <div className="right">{this.renderSince()}</div>
            </div>
          </div>
        </Link>
      </li>
    );
  }
}

Notification.defaultProps = {
  story: {}
};




