function getAvatarUrl (avatar) {
  if (avatar && avatar._url) {
    return avatar._url;
  } else if (avatar && avatar.url) {
    return avatar.url;
  }
}

const profile = {
  getDisplayName(user) {
    return user && (user.displayName || user.username) || 'Jane Doe';
  },

  getSmallAvatar(user) {
    let avatar;

    if (!!user) {
      if (user.smallAvatar) {
        avatar = user.smallAvatar;
      } else if (user.largeAvatar) {
        avatar = user.largeAvatar;
      }
    }

    return getAvatarUrl(avatar) || require('../assets/images/avatar.png');
  },

  getLargeAvatar(user) {
    let avatar;

    if (!!user) {
      if (user.largeAvatar) {
        avatar = user.largeAvatar;
      } else if (user.smallAvatar) {
        avatar = user.smallAvatar;
      }
    }

    return getAvatarUrl(avatar) || require('../assets/images/avatar.png');
  },
};

export default profile;


