import React from 'react';
import classnames from 'classnames';
export default class Hint extends React.Component {
  render() {
    let { hint, report, category, onClose } = this.props;
    if (report === 'slength' && !hint) {
      hint = 'Try and vary the sentence lengths to maintain the interest of the reader.';
    }

    return (
      <div className="hint-dialog">
        <div className="hint-content">
          <div className="hint-header">
            <a
              className="btn-close"
              aria-hidden="true"
              onClick={onClose}
            >
              <img src={require('../../assets/images/close.png')}></img>
            </a>
          </div>
          <div className={classnames('hint-body', report)}>
            <p className="hint-title">{category}</p>
            <p>{hint}</p>
          </div>
        </div>
      </div>
    );
  }
}



