import React from 'react';
import slug from 'slug';
import Select from 'react-select';
import ParseReact from 'parse-react';
import { Parse } from 'parse';
import _ from 'lodash';
import Label from '../stories/Label';
import StoryTypeConstants from '../../constants/StoryTypeConstants';

const ParseComponent = ParseReact.Component(React);

function toLabelOption(label) {
  
  switch(label.typeRaw) {

    case 0: {
      return {
        label: label.name,
        value: slug(label.name),
        type: 'label',
        objectId: label.objectId,
        color: '#BA95E3',
        typeRaw: label.typeRaw
      };
    }
    case 1: {
      return {
        label: label.name,
        value: slug(label.name),
        type: 'label',
        objectId: label.objectId,
        color: '#FFA500',
        typeRaw: label.typeRaw
      };
    }
    case 2: {
      return {
        label: label.name,
        value: slug(label.name),
        type: 'label',
        objectId: label.objectId,
        color: '#B8E887',
        typeRaw: label.typeRaw
      };
    }
    case 3: {
      return {
        label: label.name,
        value: slug(label.name),
        type: 'label',
        objectId: label.objectId,
        color: '#FFFF00',
        typeRaw: label.typeRaw
      };
    }
  }

  return {
    label: label.name,
    value: slug(label.name),
    type: 'label',
    objectId: label.objectId,
    color: '#D1ABFC',
    typeRaw: label.typeRaw
  };
}

function toStoryOption(story) {
  return {
    label: story.title,
    value: slug(story.title),
    type: 'story',
    objectId: story.objectId,
    color: '#FFFFFF'
  };
}

export default class SearchSelect extends ParseComponent {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    
  }

  observe(props, state) {
    let storyQuery = new Parse.Query('Story')
      .equalTo('statusRaw', StoryTypeConstants.PUBLIC)
      .descending('createdAt')
      .notEqualTo('deleted', true);

    if (state && state.inputValue) {
      const text = state.inputValue.toLowerCase();
      const words = text.split(' ').filter(w => w !== '');
      storyQuery.matches('title', new RegExp(text, 'i'));
    };

    const selectedLabelQuery = new Parse.Query('Label')
      .containedIn('objectId', [].concat(props.value));

    return {
      labels: new Parse.Query('Label'),
      stories: storyQuery,
      selectedLabels: selectedLabelQuery
    };
  }

  handleChange(value) {
    const labels = _(value)
      .filter((option) => option.type === 'label')
      .map(option => option.objectId)
      .value();
    const stories = _(value)
      .filter((option) => option.type === 'story')
      .map(option => option.label)
      .value();

    const storiesString = _.reduce(stories, (total, n) => `${total} ${n}`);

    const result = {
      labels,
      stories: storiesString
    };

    this.props.onChange(result);
  }

  handleInputChange(inputValue) {
    this.setState({inputValue});
  }

  renderOption(option) {
    return <span style={{ color: option.color }}>{option.value} </span>;
  }

  // renderValue(option) {
  //   return <span style={{ color: option.color }}>{option.value} </span>;
  // }


  render() {

    const { labels, stories, selectedLabels } = this.data;
    
    const labelOptions = labels.map(toLabelOption);
    labelOptions.sort((a,b) => a.value.localeCompare(b.value));

    const storyOptions = stories.map(toStoryOption);
    storyOptions.sort((a,b) => a.value.localeCompare(b.value));

    const allOptions   = labelOptions.concat(storyOptions);

    const selectedLabelOptions = selectedLabels.map(toLabelOption);

    return(

      <Select
        multi
        value={selectedLabelOptions}
        options={allOptions}
        onChange={this.handleChange}
        onInputChange={this.handleInputChange.bind(this)}
        placeholder="Search"
        optionRenderer={this.renderOption}
        // valueRenderer={this.renderValue}
        valueComponent={Label}
      />
    );
  }
}





