import { Parse } from 'parse';
import ParseAuth from './ParseAuth';
import UserTypeConstants from '../constants/UserTypeConstants';

export function userAuth (nextState, replace) {
  if (!ParseAuth.loggedIn()) {
    replace({nexPathname: nextState.location.pathname}, '/login');
  };
}

export function teacherAuth (nextState, replace) {
  const user = Parse.User.current();
  if (!user || (user && user.get('typeRaw') !== UserTypeConstants.TEACHER)) {
    replace({nexPathname: nextState.location.pathname}, '/login');
  };
}

export function loginWithToken (nextState, replace, callback) {
  const location = nextState.location;
  if (location.query && location.query.token) {
    ParseAuth.logInWithResetPasswordToken(location.query.token, (loggedIn) => {
      if (!loggedIn) {
        replace('/login');
        callback();
      } else {
        replace('/stories');
        callback();
      };
    });
  } else {
    callback();
  };
}



