import React from 'react';
import ReactDOM from 'react-dom';
import { Parse } from 'parse';
import ErrorMessages from '../layouts/ErrorMessages';

export default class ResetPassword extends React.Component {
  constructor(props) {
    super(props);

    this.resetPassword = this.resetPassword.bind(this);
    this.state = {
      error: null,
      resetSent: false
    };
  }

  render() {
    let errorMessage, contentNode;
    if (this.state.error) {
      errorMessage = <ErrorMessages messages={[this.state.error]}/>;
    };
    if (this.state.resetSent) {
      contentNode = (
        <div className="grid-block align-center">
          <p>A reset password email has been sent to your email address.</p>
        </div>
      );
    } else {
      contentNode = (
        <div>
          <input ref="email"className="email"type="email"placeholder="your@email.com" />
          <div className="grid-block align-right">
            <input className="button"type="submit"value="Reset my password"onClick={this.resetPassword} />
          </div>
          { errorMessage }
        </div>
      );
    };

    return (
      <form className="session grid-block" onKeyDown={this.keyDown}>
        <div className="grid-container small-18">
          <h1>Reset Password</h1>
          { contentNode }
        </div>
      </form>
    );
  }

  resetPassword(e) {
    e.preventDefault();
    const email = ReactDOM.findDOMNode(this.refs.email).value;
    if (!email) {
      this.setState({error: 'Please provide email'});
      return;
    };

    Parse.Cloud.run('resetPassword', {email: email}).then((result) => {
      this.setState({error: null, resetSent: true});
    }, (error) => {
      this.setState({error: error.message});
    });
  }
}



