import React from 'react';
import classNames from 'classnames';

const propTypes = {
  isSpinning: React.PropTypes.bool,
  onClick: React.PropTypes.func,
  spinnerColorClass: React.PropTypes.string,
};

const defaultProps = {
  isSpinning: false,
};

export default class SpinnerButton extends React.Component {
  render() {
    return (
      <div
        className={this.props.className}
        onClick={this.props.onClick}
      >
        <span
          className={classNames({invisible: this.props.isSpinning})}
        >{this.props.children}
        </span>
        <div
          className={
            classNames(
              'button-spinner',
              this.props.spinnerColorClass,
              {hide: !this.props.isSpinning}
            )
          }>
        </div>
      </div>
    );
  }
}

SpinnerButton.propTypes = propTypes;
SpinnerButton.defaultProps = defaultProps;



