import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import StoryStatusConstants from '../../constants/StoryStatusConstants';
import PageStatusConstants from '../../constants/PageStatusConstants';
import StoryListItem from './StoryListItem';

export default class drafts extends ParseComponent {
  observe() {
    return {
      stories: new Parse.Query('Story')
        .equalTo('creator', Parse.User.current())
        .equalTo('statusRaw', StoryStatusConstants.DRAFTED),
      pages: new Parse.Query('Page')
        .equalTo('creator', Parse.User.current())
        .equalTo('statusRaw', PageStatusConstants.DRAFTED)
        .include('story'),
    };
  }

  render() {
    const stories = this.data.stories.map((s) => {
      return (
        <StoryListItem
          key={s.objectId}
          to="/stories/new"
          {...s}
        />
      );
    });

    const pages = this.data.pages.map((p) => {
      return (
        <StoryListItem
          key={p.objectId}
          {...p.story}
        />
      );
    });

    return (
      <div className="story-list grid-block vertical">

        <div className="grid-block vertical">
          <div className="grid-block">
            <h2>New Story Draft</h2>
          </div>
          <div
            className="grid-block small-up-2 medium-up-3 large-up-4 xlarge-up-5 xxlarge-up-6"
          >
            {stories}
          </div>
        </div>
        <div className="grid-block vertical">
          <div className="grid-block">
            <h2>Submission Drafts</h2>
          </div>
          <div
            className="grid-block small-up-2 medium-up-3 large-up-4 xlarge-up-5 xxlarge-up-6"
          >
            {pages}
          </div>
        </div>
      </div>
    );
  }
};



