import React from 'react';
import moment from 'moment';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import LineChart from './LineChart';
import BarChart from './BarChart';
import classNames from 'classnames';
import UserProfile from '../../support/UserProfile';
import UserTypeConstants from '../../constants/UserTypeConstants';
import Avatar from './Avatar';
import _ from 'lodash';

const ParseComponent = ParseReact.Component(React);

function isAidContainsReportType (aid, report) {
  return aid.content.tags.filter(tag => tag.report === report).length > 0;
}

function getLabels (length, type, format) {
  return _(length)
    .range()
    .reverse()
    .map(offset => moment().subtract(offset, type).format(format))
    .value();
}

function getGradient (report) {
  const { startGradientColor, endGradientColor } = getGradientColors(report);
}

function getGradientColors (report) {
  switch (report) {
    case 'grammar':
      return {
        startGradientColor: 'rgb(248, 183, 183)',
        endGradientColor: 'rgb(255, 110, 110)'
      };
    case 'diction':
      return {
        startGradientColor: 'rgb(144, 19, 254)',
        endGradientColor: 'rgb(61, 0, 114)'
      };
    case 'length':
      return {
        startGradientColor: 'rgb(248, 231, 28)',
        endGradientColor: 'rgb(218, 149, 10)'
      };
    case 'style':
      return {
        startGradientColor: 'rgb(184, 232, 135)',
        endGradientColor: 'rgb(73, 96, 49)'
      };
    case 'consistency':
      return {
        startGradientColor: 'rgb(186, 149, 227)',
        endGradientColor: 'rgb(108, 44, 217)'
      };
    default:
      break;
  }
}

function groupedAids (aids) {
  return _(aids)
    .filter(aid => aid.content.tags.length > 0)
    .reduce((object, aid) => {
      const tags = aid.content.tags.map(tag => {
        tag.createdAt = aid.createdAt;
        return tag;
      });
      const result = _.mapValues(object, (value, key) => {
        let report;
        switch (key) {
          case 'diction':
            report = 'dva';
            break;
          case 'length':
            report = 'slength';
            break;
          case 'style':
            report = 'passive';
            break;
          default:
            report = key;
        }
        return value.concat(tags.filter(tag => tag.report === report));
      });
      return result;
    }, {
      grammar: [],
      diction: [],
      length: [],
      style: [],
      consistency: [],
    });
}

export default class UserStats extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      activeButton: 'week',
      grammarChartType: 'frequency',
      styleChartType: 'frequency',
      lengthChartType: 'frequency',
      consistencyChartType: 'frequency',
      dictionChartType: 'frequency',
    };

    this.handlePrint = this.handlePrint.bind(this);
  }

  observe(props, state) {
    const user = {
      __type: 'Pointer',
      className: '_User',
      objectId: props.params.id
    };
    const pageQuery = new Parse.Query('Page').equalTo('creator', user);
    const storyQuery = new Parse.Query('Story').equalTo('creator', user);
    const aid1Query = new Parse.Query('Aid');
    aid1Query.matchesQuery('story', storyQuery);
    const aid2Query = new Parse.Query('Aid');
    aid2Query.matchesQuery('page', pageQuery);
    return {
      users: new Parse.Query(Parse.User).equalTo('objectId', props.params.id),
      aids: Parse.Query.or(aid1Query, aid2Query),
    };
  }

  handlePrint() {
    window.print();
  }

  handleClickType(type) {
    this.setState({activeButton: type});
  }

  handleClickChartType(category, type) {
    const newState = {};
    newState[`${category}ChartType`] = type;
    this.setState(newState);
  }

  consolidateFrequencyData(aids, type) {
    let momentType, labels;
    let numberOfXs;
    switch (type) {
      case 'day':
        momentType = 'day';
        numberOfXs = moment().diff(moment().subtract(1, 'month'), 'days');
        labels = getLabels(numberOfXs, type, 'M/D');
        break;
      case 'month':
        momentType = 'month';
        numberOfXs = 12;
        labels = getLabels(numberOfXs, type, 'MMMM');
        break;
      case 'week':
        momentType = 'isoWeek';
        numberOfXs = 12;
        labels = getLabels(numberOfXs, type, 'M/D');
        break;
      default:
        break;
    }

    return _(groupedAids(aids))
      .mapValues((results, key) => {
        const data = _(numberOfXs)
          .range()
          .reverse()
          .map(offset => {
            const offsetMoment = moment().subtract(offset, type);
            const start = moment(offsetMoment).startOf(momentType);
            const end = moment(offsetMoment).endOf(momentType);

            return results
              .filter(result => moment(result.createdAt).isBetween(start, end))
              .length;
          })
          .value();

        const { startGradientColor, endGradientColor } = getGradientColors(key);

        return {
          startGradientColor,
          endGradientColor,
          data: {
            labels: labels,
            datasets: [
              {
                label: key,
                pointColor: 'rgba(220,220,220,1)',
                pointStrokeColor: '#fff',
                pointHighlightFill: '#fff',
                pointHighlightStroke: 'rgba(220,220,220,1)',
                data
              }
            ],
          },
        };
      })
      .value();
  }

  consolidateCategoryData(aids, type) {
    return _(groupedAids(aids))
      .mapValues((tags, key) => {
        const categories = _(tags)
          .map(tag => tag.categoryDisplayName)
          .uniq()
          .value();
        const { startGradientColor, endGradientColor } = getGradientColors(key);

        let momentType, numberOfXs, filteredTags, format;

        switch (type) {
          case 'day':
            momentType = 'day';
            numberOfXs = moment().diff(moment().subtract(1, 'month'), 'days');
            format = 'M/D';
            break;
          case 'month':
            momentType = 'month';
            numberOfXs = 12;
            format = 'MMMM';
            break;
          case 'week':
            momentType = 'isoWeek';
            numberOfXs = 12;
            format = 'M/D';
            break;
          default:
            break;
        }

        return {
          startGradientColor,
          endGradientColor,
          data: {
            labels: categories,
            datasets: _(numberOfXs)
              .range()
              .reverse()
              .map(offset => {
                const offsetMoment = moment().subtract(offset, type);
                const start = moment(offsetMoment).startOf(momentType);
                const end = moment(offsetMoment).endOf(momentType);
                return {
                  label: offsetMoment.format(format),
                  pointColor: 'rgba(220,220,220,1)',
                  pointStrokeColor: '#fff',
                  pointHighlightFill: '#fff',
                  pointHighlightStroke: 'rgba(220,220,220,1)',
                  data: categories
                    .map(category => {
                      return tags
                        .filter(tag => {
                          const createdAt = moment(tag.createdAt);
                          return (tag.categoryDisplayName === category) && (createdAt.isBetween(start, end));
                        })
                        .length;
                    })
                };
              })
              .value()
          }
        };

      })
      .value();
  }

  renderHeader(user) {

    return (
      <div className="grid-block align-center" key={user.objectId}>
        <div className="small-20 grid-block profile">
          <div className="grid-block small-8">
            <Avatar
              userId={user.objectId}
              imageUrl={UserProfile.getLargeAvatar(user)}
              title={UserProfile.getDisplayName(user)}
            />
            <div className="grid-block v-align content">
              <div className="grid-block vertical">
                <div>
                  {UserProfile.getDisplayName(user)}
                </div>
                <div>
                  {user.typeRaw === UserTypeConstants.STUDENT ? 'Student' : 'Teacher'}
                </div>
              </div>
            </div>
          </div>
          <div className="grid-block align-right">
            <div className="grid-block v-align small-6">
              <a
                className={classNames({'active': this.state.activeButton === 'month'})}
                onClick={this.handleClickType.bind(this, 'month')}
              >
                Month
              </a>
            </div>
            <div className="grid-block v-align small-6">
              <a
                className={classNames({'active': this.state.activeButton === 'week'})}
                onClick={this.handleClickType.bind(this, 'week')}
              >
                Week
              </a>
            </div>
            <div className="grid-block v-align small-6">
              <a
                className={classNames({'active': this.state.activeButton === 'day'})}
                onClick={this.handleClickType.bind(this, 'day')}
              >
                Day
              </a>
            </div>
            <div className="grid-block v-align small-6 shrink noprint">
              <a className="text-right" onClick={this.handlePrint}>
                <img src={require('../../assets/images/print.png')} alt="print"/>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderGraphs(user, aids, type) {
    const frequencyData = this.consolidateFrequencyData(aids, type);
    const categoryData = this.consolidateCategoryData(aids, type);
    // const { grammar, diction, length, style, consistency } =
    //   _(this.consolidateFrequencyData(aids, type))
    //     .mapValues((data, key) => {
    //       let chart;
    //       if (this.state[`${key}ChartType`] === 'category') {
    //         chart = (
    //           <LineChart
    //             {...data}
    //           />
    //         );
    //       } else {
    //         chart = (
    //           <BarChart

    //           />
    //         );
    //       }
    //     })
    //     .value();
    const graphs = ['grammar', 'diction', 'length', 'style', 'consistency']
      .map(report => {
        let chart;
        if (this.state[`${report}ChartType`] === 'frequency') {
          chart = (
            <LineChart {...frequencyData[report]} />
          );
        } else {
          chart = (
            <BarChart {...categoryData[report]} />
          );
        }
        return (
          <div
            className={classNames(report, 'grid-block card card-block vertical')}
            key={report}
          >
            <div className="grid-block chart-header">
              <div className="v-align chart-name">
                { _.capitalize(report) }
              </div>
              <div className="v-align">
                <ul className="button-group segmented">
                  <li
                    className={classNames({'is-active': this.state[`${report}ChartType`] === 'frequency'})}
                  >
                    <a
                      onClick={this.handleClickChartType
                        .bind(this, report, 'frequency')
                      }
                    >
                      Frequency
                    </a>
                  </li>
                  <li
                    className={classNames({'is-active': this.state[`${report}ChartType`] === 'category'})}
                  >
                    <a
                      onClick={this.handleClickChartType
                        .bind(this, report, 'category')
                      }
                    >
                      Category
                    </a>
                  </li>
                </ul>
              </div>
              <div className="grid-block v-align align-right chart-date">
                {`as of ${moment().format('MMM Do YYYY')}`}
              </div>
            </div>
            <div className="grid-block chart-body">
              {chart}
            </div>
          </div>
        );
      });

    return (
      <div className="grid-block align-center" key={user.objectId}>
        <div className="small-20 grid-block vertical">
          <div className="grid-block card card-block vertical metrics">
            <div className="grid-block chart-header">
              <div className="v-align chart-name">
                Writing Metrics
              </div>
              <div className="grid-block v-align align-right chart-date">
                {`as of ${moment().format('MMM Do YYYY')}`}
              </div>
            </div>
            <div className="grid-block">
              <div className="grid-block vertical border-right stats">
                <div className="text-center stat">
                  {user.submissionCount}
                </div>
                <div className="text-center">
                  Submissions Written
                </div>
              </div>
              <div className="grid-block vertical stats">
                <div className="text-center stat">{user.storyCount}</div>
                <div className="text-center">Stories Created</div>
              </div>
            </div>
          </div>
          { graphs }
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="user-stats grid-block vertical print">
        {this.data.users.map(user => this.renderHeader(user))}
        {this.data.users.map(user =>
          this.renderGraphs(user, this.data.aids, this.state.activeButton)
        )}
      </div>
    );
  }
}



