import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import UserProfile from '../../support/UserProfile';
import NotificationCenter from './NotificationCenter';

export default class UserNavigationBar extends ParseComponent {
  observe() {
    return {
      users: new Parse.Query(Parse.User).equalTo('objectId', this.props.params.id),
      user: ParseReact.currentUser,
    };
  }
  render() {
    const user = this.data.users[0];

    return (
      <TitleBar>
        <TitleBar.Left>
          <BackButton />
        </TitleBar.Left>
        <TitleBar.Center>
          {UserProfile.getDisplayName(user)}
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            <div className="grid-block">
              <NotificationCenter />
            </div>
            <div className="grid-block">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
      </TitleBar>
    );
  }
}



