import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import Analytics from '../../support/Analytics';
import Page from './Page';
import SlickSlider from './SlickSlider';
import UserTypeConstants from '../../constants/UserTypeConstants';

export default class Submissions extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      isShowing: props.isShowing,
      isSmall: this.props.isSmall
    };
    this.data = {
      pages: props.pages,
      likes: props.likes
    };
  }

  observe(props) {
    let story = new Parse.Object('Story');
    story.id = props.storyId;
    let refPageId = props.refPageId;
    const pageQuery = new Parse.Query('Page')
      .equalTo('story', story)
      .equalTo('refPageId', refPageId)
      .equalTo('statusRaw', 0)
      .ascending('createdAt')
      .include('creator');
    if (props.editingPage) {
      pageQuery.notEqualTo('objectId', props.editingPage.objectId);
    }
    return {
      pages: pageQuery,
      aids: new Parse.Query('Aid').matchesQuery('page', pageQuery),
      user: ParseReact.currentUser
    };
  }

  toggleShowHide() {
    this.setState({isShowing: !this.state.isShowing, isSmall: !this.state.isShowing});

    Analytics.track('Page', 'Submissions', 'Show: ' + !this.state.isShowing, {
      isShowing: !this.state.isShowing
    });
  }

  showLastSlide() {
    this.setState({isShowing: true, settings: {initialSlide: this.data.pages.length - 1}});
  }

  handleContentUpdate(page, value) {
    ParseReact.Mutation.Set(page, value).dispatch();
  }

  handleEdit(page) {
    this.props.onEdit(page);
  }

  renderPending() {
    if (!this.state.isShowing || !this.data.pages.length) {
      return null;
    }

    return (
      <div className="pending-pages small-up-3 grid-block">
        <SlickSlider settings={{
          className: 'submissions-slider',
          slidesToShow: 1,
          responsive: [{
            breakpoint: 1300,
            settings: {
              slidesToShow: 2,
            }
          }, {
            breakpoint: 1024,
            settings: {
              slidesToShow: 1
            }
          }],
          ...this.state.settings
        }}>
          {this.data.pages.map((p,i) => {
            const likedPages = this.data.likes &&
              this.data.likes.filter((like) => {
                return like.page !== undefined
                        && like.page.id.objectId === p.id.objectId;
              });

            const aids = this.data.aids &&
              this.data.aids.filter((aid) => aid.page.objectId === p.objectId);
            return (
              <div key={p.id.objectId}>
                <Page {...p}
                  isSmall={false}
                  isLiked={likedPages.length > 0}
                  aids={aids}
                  currentUser={this.data.user}
                  writeAssistOn={this.props.writeAssistOn}
                  activeReports={this.props.activeReports}
                  onContentUpdate={this.handleContentUpdate.bind(this, p)}
                  hideAvatar={this.data.user.objectId !== p.creator.objectId}
                  hideUsername={this.data.user.typeRaw !== UserTypeConstants.TEACHER}
                  allowEdit={this.data.user.objectId === p.creator.objectId || this.data.user.typeRaw === UserTypeConstants.TEACHER}
                  onEdit={this.handleEdit.bind(this, p)}
                />
              </div>
            );
          }, this)}
        </SlickSlider>
      </div>
    );
  }

  render() {
    return (
      <div className="submissions">
        <div className="show-hide grid-container">
          <div className="button hollow" onClick={this.toggleShowHide.bind(this)}>
            {this.state.isShowing ? 'Hide submissions' : 'See current submissions'}
          </div>
        </div>

        {this.renderPending()}
      </div>
    );
  }
}

Submissions.propTypes = {
  isShowing: React.PropTypes.bool.isRequired,
  isSmall: React.PropTypes.bool.isRequired,
  pages: React.PropTypes.array.isRequired,
  likes: React.PropTypes.array.isRequired
};

Submissions.defaultProps = {
  isShowing: false,
  isSmall: true,
  pages: [],
  likes: []
};



