import React from 'react';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import Dropdown from './Dropdown';
import MenuItem from './MenuItem';
import { Link } from 'react-router';
import UserProfile from '../../support/UserProfile';
import UserTypeConstants from '../../constants/UserTypeConstants';

export default class UserDropDown extends ParseComponent {
  constructor(props) {
    super(props);

    this.renderTeacherLinks = this.renderTeacherLinks.bind(this);
  }

  observe(props, state) {
    return {
      user: ParseReact.currentUser,
    };
  }

  renderTeacherLinks() {
    return [(
        <MenuItem key={0}>
          <Link to="/admin/labels">Labels</Link>
        </MenuItem>
      ), (
        <MenuItem key={1}>
          <Link to="/admin/users">Users</Link>
        </MenuItem>
      )
    ];
  }

  render() {
    const { user } = this.data;

    let teacherComponents;

    if (user && user.typeRaw === UserTypeConstants.TEACHER) {
      teacherComponents = this.renderTeacherLinks();
    };

    return (
      <Dropdown
        iconUrl={user && UserProfile.getSmallAvatar(user)}
        avatar
      >
        <li className="dropdown-header">
          <h1 className="signed-in">Signed in as</h1>
          <h1 className="username">{UserProfile.getDisplayName(user)}</h1>
        </li>
        <MenuItem>
          <Link to="/stories/bookmarks">Bookmarks</Link>
        </MenuItem>
        <MenuItem>
          <Link to="/me/edit">Edit Profile</Link>
        </MenuItem>
        {teacherComponents}
        <MenuItem className="center">
          <div className="grid-block align-center shrink center">
            <Link to="logout" className="alert">Logout</Link>
          </div>
        </MenuItem>
      </Dropdown>
    );
  }
}



