import React from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import Analytics from '../../support/Analytics';
import Highlight from './Highlight';
import Hintify from '../../support/hintify';
import LikeButton from './LikeButton';
import UserProfile from '../../support/UserProfile';
import format from '../../support/HtmlFormatter';
import Avatar from '../users/Avatar';
import UserTypeConstants from '../../constants/UserTypeConstants';
import PageStatusConstants from '../../constants/PageStatusConstants';
import HighlightButton from '../medium/HighlightButton';
import Editor from 'react-medium-editor';
import sanitizeHtml from 'sanitize-html';
import AidBlock from '../draft/AidBlock';
import { sliceTextWithTags } from '../../modifiers/insertAidBlocks';
import Hint from './Hint';
import rangy from 'rangy';
import Immutable from 'immutable';
import 'rangy/lib/rangy-textrange';

export default class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      creator: this.props.creator,
      image: this.props.image,
      refPageId: this.props.refPageId,
      commentCount: this.props.commentCount,
      isCommentSelected: false,
      isEditing: false,
      tags: [],
      hidedHints: Immutable.Set(),
      position: null
    };

    this.handleClickComment = this.handleClickComment.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.handleCancelEdit = this.handleCancelEdit.bind(this);
    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.renderHints = this.renderHints.bind(this);
    this.setHint = this.setHint.bind(this);

    // this.handleContentChange = this.handleContentChange.bind(this);
  }

  componentDidMount() {
    this.setHint(this.props);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.innerHTML !== nextProps.innerHTML) {
      this.setHint(nextProps);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    this.setHint(this.props);
  }

  componentWillUnmount() {
    this.hint && this.hint.destroy();
    this.hint = null;
  }

  setHint(props) {
    this.hint && this.hint.destroy();
    this.hint = null;
    const { aids, content, writeAssistOn, activeReports, innerHTML } = props;
    const { isEditing } = this.state;

    if (!writeAssistOn && innerHTML && !isEditing) {
      this.hint = new Hintify(ReactDOM.findDOMNode(this), innerHTML, [], false, [], 'span.highlight');
    }
    if (this.hint) {
      this.hint.showHint();
    }
  }


  smallAvatarUrl() {
    if (this.props.creator && this.props.creator.smallAvatar) {
      return (<img src={this.state.creator.smallAvatar._url} />);
    } else {
      return (<img src={require('../../assets/images/avatar.png')} />);
    }
  }

  imageContent() {
    if (this.state.image && this.state.refPageId) {
      return (<div><img src={this.state.image._url} /></div>);
    }
  }

  handleClickComment() {
    this.props.onClickComment(this.props.objectId);
    this.setState({isCommentSelected: !this.state.isCommentSelected});

    Analytics.track('Page', 'Comment', 'Show: ' + this.state.isCommentSelected, {
      objectId: this.props.objectId,
      value: this.state.commentCount,
      commentCount: this.state.commentCount
    });
  }

  handleEdit() {
    this.setState({isEditing: true});
    this.props.onStartEdit && this.props.onStartEdit();
  }

  handleCancelEdit() {
    this.setState({isEditing: false});
    this.props.onCancelEdit && this.props.onCancelEdit();
  }

  handleUpdate() {
    this.setState({isEditing: false});
    const node = ReactDOM.findDOMNode(this.refs.editor);
    const innerHTML = node.innerHTML;
    const range = rangy.createRange();
    range.selectNodeContents(node);
    const sanitized = sanitizeHtml(innerHTML, {
      allowedTags: sanitizeHtml.defaults.allowedTags.concat(['span', 'p']),
      allowedClasses: {
        'span': ['highlight']
      },
      allowedAttributes: {
        'span': ['data-category', 'data-report', 'data-hint']
      }
    });

    const value = {
      content: range.text(),
      innerHTML: sanitized
    };
    this.props.onContentUpdate(value);
  }

  deselectComment() {
    this.setState({isCommentSelected: false});
  }

  incrementCommentCount() {
    this.setState({commentCount: this.state.commentCount + 1});
  }

  handleChange(text, medium) {
    this.setState({content: text});
  }

  toggleStudentEdit(newState) {
    this.props.onContentUpdate({allowStudentEdit: newState});
  }

  showTags(tags, e) {
    const { clientX: x, clientY: y } = e;
    this.setState({tags, position: {x,y}, hidedHints: Immutable.Set()});
  }

  closeTag(index) {
    this.setState({
      hidedHints: this.state.hidedHints.add(index)
    });
  }

  renderHints() {
    const { tags, position, hidedHints } = this.state;
    const { writeAssistOn, activeReports } = this.props;

    if (!writeAssistOn || !tags.length || !position) return;

    const { top, left, right } = this.refs.container.getBoundingClientRect();
    const activeTags = tags.filter(tag => activeReports.indexOf(tag.report) !== -1);

    const xOffSet = right/2 - position.x;

    return (
      <div
        className="hints grid-block"
        style={{
          position: 'absolute',
          zIndex: 999,
          top: position.y - top + 30,
          left: position.x + xOffSet
        }}
      >
        {activeTags.map((tag, i) => (
          !hidedHints.has(i) &&
          <div className="hint grid-block" key={i}>
            <Hint
              category={tag.categoryDisplayName}
              hint={tag.hint}
              report={tag.report}
              onClose={this.closeTag.bind(this, i)}
            />
          </div>
        ))}
      </div>
    );
  }

  renderPageContent() {
    let { content, aids, writeAssistOn, innerHTML, activeReports } = this.props;
    const splittedContent = content ? content.split('\n') : [''];
    if (writeAssistOn && aids.length) {
      const tags = aids[0].content.tags;
      const activeTags = tags.filter(tag => activeReports.indexOf(tag.report) !== -1);
      if (activeTags.length) {
        const result = sliceTextWithTags(content, activeTags);
        return (
          <p>
            {result.map(({text, tags}, i) => {
              if (tags.length > 0) {
                return (
                  <AidBlock
                    tags={tags}
                    key={i}
                    onClick={this.showTags.bind(this, tags)}
                  >
                    {text}
                  </AidBlock>
                );
              } else {
                return text;
              }
            })}
          </p>
        );
      }
    } else {
      if (innerHTML) {
        if (innerHTML.indexOf('<p>') === -1) {
          innerHTML = `<p>${innerHTML}</p>`;
        }
        return <div dangerouslySetInnerHTML={{__html: innerHTML}} />;
      }
    }
    return splittedContent.map((content, i) => (
      <p>
        {content}
      </p>
    ));
  }

  render() {
    const { isEditing, editingContent } = this.state;
    const { creator, onClickComment, allowStudentEdit, currentUser, statusRaw, className, edited } = this.props;

    let commentButtonClasses = classNames({
      'button': true,
      'comment-button': true,
      'active': this.state.isCommentSelected,
      'noprint': true
    });

    let commentButton;
    if (onClickComment) {
      commentButton = (
        <div className={commentButtonClasses} onClick={this.handleClickComment}>
          <i className="fa fa-comment"></i>
          <span className="comment-count noprint">{this.state.commentCount}</span>
        </div>
      );
    }

    let avatar;

    if (!this.props.hideAvatar) {
      avatar = (
        <Avatar
          userId={creator && creator.objectId}
          imageUrl={UserProfile.getSmallAvatar(creator)}
          title={UserProfile.getDisplayName(creator)}
        />
      );
    };

    let username;
    if (!this.props.hideUsername && creator) {
      username = UserProfile.getDisplayName(creator);
    }

    let isTeacher, allowEdit, editorOptions;
    if (currentUser && creator) {
      isTeacher = currentUser.typeRaw === UserTypeConstants.TEACHER;
      allowEdit = isTeacher
        || (statusRaw === PageStatusConstants.APPROVED && allowStudentEdit && currentUser.objectId === creator.objectId && !edited)
        || (className === 'Page' && statusRaw === PageStatusConstants.PENDING && currentUser.objectId === creator.objectId)
        || (className === 'Story' && allowStudentEdit && currentUser.objectId === creator.objectId && !edited);
      if (isTeacher) {
        editorOptions = {
          toolbar: {
            buttons: ['highlighter']
          },
          buttonLabels: 'fontawesome',
          extensions: {
            'highlighter': new HighlightButton()
          }
        };
      } else {
        editorOptions = {
          toolbar: false
        };
      }
    }

    let style;

    if (this.props.writeAssistOn) {
      style = {
        overflow: 'visible'
      };
    }

    let { innerHTML } = this.props;
    if (innerHTML && innerHTML.indexOf('<p>') === -1) {
      innerHTML = `<p>${innerHTML}</p>`;
    }

    return (
      <div
        className="page small-24 large-24 grid-block"
        ref="container"
        style={style}
      >
        {!this.props.hideLine && (<div className="line noprint"></div>)}
        <div className="small-3 print">
          {avatar}
        </div>
        <div className="username noprint">
          {username}
        </div>
        {commentButton}
        {currentUser && currentUser.objectId !== creator.objectId &&
          <LikeButton {...this.props} />
        }

        <div className="content small-18">
          {!isEditing &&
            this.renderPageContent()
          }
          {!isEditing && this.imageContent()}
          {isEditing &&
            <div className="textarea-container grid-block">
              <Editor
                ref="editor"
                className="grid-block textarea"
                onChange={this.handleChange}
                showHint={this.props.writeAssistOn}
                text={innerHTML || this.props.content}
                options={editorOptions}
              />
            </div>
          }
        </div>
        <div className="small-3 edit noprint">
          {isEditing &&
            <div className="button" onClick={this.handleCancelEdit}>
              <img src={require('../../assets/images/CancelEdit.png')} alt="Cancel Edit"/>
            </div>
          }
          {isEditing &&
            <div className="button" onClick={this.handleUpdate}>
              <img src={require('../../assets/images/Approve.png')} alt="Approve"/>
            </div>
          }
          {allowEdit && !isEditing && this.props.statusRaw !== PageStatusConstants.HISTORY &&
            <div className="edit button" onClick={this.handleEdit}>
              <div className="description">Edit</div>
              <img src={require('../../assets/images/Edit.png')} alt="Edit"/>
            </div>
          }
          {allowEdit && !isEditing && !allowStudentEdit && isTeacher && (statusRaw === PageStatusConstants.APPROVED || className === 'Story') &&
            <div className="edit button" onClick={this.toggleStudentEdit.bind(this, true)}>
              <div className="description">Enable Student Edit</div>
              <img src={require('../../assets/images/StudentEdit.png')} alt="Student Edit"/>
            </div>
          }
          {allowEdit && !isEditing && allowStudentEdit && isTeacher && (statusRaw === PageStatusConstants.APPROVED || className === 'Story') &&
            <div className="edit button" onClick={this.toggleStudentEdit.bind(this, false)}>
              <div className="description">Disable Student Edit</div>
              <img src={require('../../assets/images/StudentEditOn.png')} alt="Disable Student Edit"/>
            </div>
          }
        </div>
        {this.renderHints()}
      </div>
    );
  }
}

Page.defaultProps = { commentCount: 0 };

Page.propTypes = {
  onClickComment: React.PropTypes.func,
  onContentUpdate: React.PropTypes.func
};



