import _ from 'lodash';
import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import NotificationSystem from '../widgets/NotificationSystem';
import classNames from 'classnames';
import ParseAuth from '../../support/ParseAuth';

import Analytics from '../../support/Analytics';
import UserProfile from '../../support/UserProfile';
import Labels from './Labels';
import PageTextArea from '../pages/PageTextArea';
import EditableStoryHeader from './EditableStoryHeader';
import LabelTypeConstants from '../../constants/LabelTypeConstants';
import StoryTypeConstants from '../../constants/StoryTypeConstants';
import StoryStatusConstants from '../../constants/StoryStatusConstants';
import UserTypeConstants from '../../constants/UserTypeConstants';
import { Link } from 'react-router';
import Perf from 'react-addons-perf';

global.Perf = Perf;
let askUserAboutEmptyText = false;

const defaultProps = {
  title: null,
  files: null,
};

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class NewStory extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      title: props.title,
      files: props.files,
      content: props.content,
      saveDraft: false,
      classOptions: [],
      genreOptions: [],
      assignmentOptions: [],
      miscOptions: [],
      storyId: null,
      creating: false,
      saving: false,
      assisting: false,
      _notificationSystem: null,
      aids: null,
      imageUrl: null,
      titleModified: false,
      contentModified: false,
      classOptionsModified: false,
      genreOptionsModified: false,
      assignmentOptionsModified: false,
      miscOptionsModified: false,
      imageUrlModified: false,
      characterLimit: 400
    };

    this.routerWillLeave = this.routerWillLeave.bind(this);
    this.getData = this.getData.bind(this);
    this.uploadImage = this.uploadImage.bind(this);
    this.createNewStory = this.createNewStory.bind(this);
    this.updateStory = this.updateStory.bind(this);
    this.saveDraft = this.saveDraft.bind(this);
    this.handleLabelChange = this.handleLabelChange.bind(this);
    this.handleTitleChange = this.handleTitleChange.bind(this);
    this.handleDrop = this.handleDrop.bind(this);
    this.handleContentChange = this.handleContentChange.bind(this);
    this.handleWriteAssist = this.handleWriteAssist.bind(this);
    this.handleSaveDraft = this.handleSaveDraft.bind(this);
    this.validate = this.validate.bind(this);
    this.handleCreate = this.handleCreate.bind(this);
    this.handleCharacterLimitChange = this.handleCharacterLimitChange.bind(this);
    this.generateNotification = this.generateNotification.bind(this);
  }

  routerWillLeave(nextLocation) {
    if (this.state.titleModified
      || this.state.contentModified
      || this.state.classOptionsModified
      || this.state.genreOptionsModified
      || this.state.assignmentOptionsModified
      || this.state.miscOptionsModified
      || this.state.imageUrlModified
    ) {
      this.saveDraft();
    };
  }

  observe(props, state) {
    let query = {
      labels: new Parse.Query('Label'),
      user: ParseReact.currentUser,
    };

    if (props.location.query && props.location.query.id) {
      query.stories = (new Parse.Query('Story'))
        .equalTo('objectId', props.location.query.id);
    };

    return query;
  }

  componentDidMount() {
    const { route } = this.props;
    const { router } = this.context;
    router.setRouteLeaveHook(route, this.routerWillLeave);
    this._notificationSystem = this.refs.notificationSystem;
  }

  getData() {
    let storyId, initialValue, title, imageUrl;
    let classOptions = this.state.classOptions;
    let genreOptions = this.state.genreOptions;
    let assignmentOptions = this.state.assignmentOptions;
    let miscOptions = this.state.miscOptions;

    if (this.data.stories && this.data.stories.length) {
      const story = this.data.stories[0];
      const ids = story.labels && story.labels.map((label) => label && label.objectId);
      const savedLabels = _(this.data.labels)
        .filter((label) => {
          return ids && ids.indexOf(label.objectId) !== -1;
        })
        .groupBy('typeRaw')
        .value();

      storyId = story.objectId;

      if (!this.state.contentModified) {
        initialValue = story.content;
      };
      if (!this.state.titleModified) {
        title = story.title;
      };

      if (!this.state.classOptionsModified) {
        classOptions = savedLabels[LabelTypeConstants.CLASS] || [];
      }
      if (!this.state.genreOptionsModified) {
        genreOptions = savedLabels[LabelTypeConstants.GENRE] || [];
      }
      if (!this.state.assignmentOptionsModified) {
        assignmentOptions = savedLabels[LabelTypeConstants.ASSIGNMENT] || [];
      }
      if (!this.state.miscOptionsModified) {
        miscOptions = savedLabels[LabelTypeConstants.MISC] || [];
      }
      if (!this.state.imageUrlModified) {
        imageUrl = story.image && (story.image._url || story.image.url);
      }
    };
    return {
      storyId: storyId || this.state.storyId,
      content: this.state.content || initialValue,
      initialValue,
      title: title || this.state.title,
      selectedLabels: classOptions.concat(genreOptions).concat(assignmentOptions).concat(miscOptions),
      imageUrl: imageUrl || this.state.imageUrl,
    };
  }

  uploadImage() {
    let p;
    if (this.state.files) {
      const image = new Parse.File('file', this.state.files[0]);
      p = image.save();
    } else {
      p = Parse.Promise.as();
    };
    return p;
  }

  createNewStory(status) {
    const data = this.getData();

    return this.uploadImage().then((image) => {
      return ParseReact.Mutation.Create('Story', {
        approver: Parse.User.current(),
        creator: Parse.User.current(),
        title: data.title,
        content: data.content,
        statusRaw: status,
        image: image,
        labels: this.getData().selectedLabels,
        typeRaw: StoryTypeConstants.PUBLIC,
        characterLimit: this.state.characterLimit
      }).dispatch();
    }).then((story) => {
      return Parse.Cloud.run('getAid', {text: data.content}).then((response) => {
        return ParseReact.Mutation.Create('Aid', {
          content: JSON.parse(response.text),
          story
        }).dispatch();
      }).then((aid) => {
        return aid.story;
      });
    });
  }

  updateStory(options) {
    return this.uploadImage().then((image) => {
      if (image) {
        options.image = image;
      };
      return ParseReact.Mutation.Set(this.data.stories[0], {...options}).dispatch();
    });
  }

  saveDraft() {
    const data = this.getData();
    if (this.state.content && this.state.content.length > 1000) {
      return;
    } else if (!this.state.saving) {
      if (!data.storyId) {
        this.createNewStory(StoryStatusConstants.DRAFTED);
      } else {
        this.updateStory({
          content: data.content,
          labels: this.getData().selectedLabels,
          title: data.title,
          characterLimit: this.state.characterLimit
        });
      };
    };
  }

  handleLabelChange(type, value) {
    this.setState({
      classOptions: type === LabelTypeConstants.CLASS ? value : this.state.classOptions,
      genreOptions: type === LabelTypeConstants.GENRE ? value : this.state.genreOptions,
      assignmentOptions: type === LabelTypeConstants.ASSIGNMENT ? value : this.state.assignmentOptions,
      miscOptions: type === LabelTypeConstants.MISC ? value : this.state.miscOptions,
      classOptionsModified: type === LabelTypeConstants.CLASS || this.state.classOptionsModified,
      genreOptionsModified: type === LabelTypeConstants.GENRE || this.state.genreOptionsModified,
      assignmentOptionsModified: type === LabelTypeConstants.ASSIGNMENT || this.state.assignmentOptionsModified,
      miscOptionsModified: type === LabelTypeConstants.MISC || this.state.miscOptionsModified,
    });
  }

  handleTitleChange(title) {
    this.setState({title: title, titleModified: true});
  }

  handleDrop(files) {
    this.setState({files: files});
  }

  handleCharacterLimitChange(e) {
    const value = parseInt(e.target.value);
    this.setState({characterLimit: value});
  }

  handleContentChange(text) {
    this.setState({content: text, contentModified: true});
  }

  handleWriteAssist(e) {
    let assisting = false;
    if (!this.state.showHint) {
      assisting = true;
      Parse.Cloud.run('getAid', {text: this.state.content}).then((response) => {
        this.setState({aids: JSON.parse(response.text), showHint: true, assisting: false});
      }, error => {
        console.error('write assist error: ', error);
        this.generateNotification(
          'Clio is sick right now! Please try again later',
          'error'
        );
      });

      Analytics.track('Story', 'Clio', 'On', {
        state: 'Create Story'
      });
    };
    this.setState({assisting, showHint: !this.state.showHint});
  }

  generateNotification(message, level) {
    this._notificationSystem.addNotification({
      message,
      level
    });
  }

  handleSaveDraft(e) {
    e && e.preventDefault();
    const data = this.getData();
    if (this.state.content && this.state.content.length > 1000) {
      this._notificationSystem.addNotification({
        message: 'Your story content must be less than 1000 characters!',
        level: 'error'
      });
    } else if (!this.state.saving) {
      if (!data.storyId) {
        this.createNewStory(StoryStatusConstants.DRAFTED).then((result) => {
          this.setState({storyId: result.objectId, saving: false});
          this.refreshQueries();
        });
        this.setState({saveDraft: true});
      } else {
        this.setState({saving: true});
        this.updateStory({
          content: data.content,
          labels: this.getData().selectedLabels,
          title: data.title,
        }).then((result) => {
          this.setState({
            saving: false,
            titleModified: false,
            contentModified: false,
            classOptionsModified: false,
            genreOptionsModified: false,
            assignmentOptionsModified: false,
            miscOptionsModified: false,
            imageUrlModified: false,
          });
        });

        Analytics.track('Story', 'SaveDraftStory', 'Create Story', {
          saveAgain: this.data.stories && this.data.stories.length > 0
        });
      };
    };
  }

  validate() {
    const data = this.getData();
    if (!data.title) {
      this._notificationSystem.addNotification({
        message: 'Your story must have a Title!',
        level: 'error'
      });
      return false;
    } else if (!this.state.files && !data.imageUrl){
      this._notificationSystem.addNotification({
        message: 'Your story must have a picture!',
        level: 'error'
      });
      return false;
    } else if (!data.content && askUserAboutEmptyText === false) {
      askUserAboutEmptyText = true;
      this._notificationSystem.addNotification({
        message: 'Did you mean to create the story without any text? If so, then click create again.',
        level: 'error'
      });
      return false;
    } else if (data.content && this.state.content.length > 1000) {
      this._notificationSystem.addNotification({
        message: 'Your story content must be less than 1000 characters!',
        level: 'error'
      });
      return false;
    } else {
      askUserAboutEmptyText = false;
      return true;
    }
  }

  handleCreate(e) {
    const data = this.getData();
    e.preventDefault();
    if (this.validate() && !this.state.creating) {
      this.setState({creating: true});
      if (!data.storyId) {
        this.createNewStory(StoryStatusConstants.ONGOING).then((result) => {
          this.setState({creating: false});
          this.context.router.push(`/stories/${result.objectId}`);
        }, error => {
          console.error('createNewStory error: ', error);
          this.generateNotification(
            'Cannot save story. Plase try again',
            'error'
          );
          this.setState({creating: false});
        });
      } else {
        this.setState({saving: true});
        this.updateStory({
          content: data.content,
          title: data.title,
          labels: this.getData().selectedLabels,
          statusRaw: StoryStatusConstants.ONGOING
        }).then((result) => {
          this.setState({
            saving: false,
            creating: false,
            titleModified: false,
            contentModified: false,
            classOptionsModified: false,
            genreOptionsModified: false,
            assignmentOptionsModified: false,
            miscOptionsModified: false,
            imageUrlModified: false,
          });
          this.context.router.pushState(null, `/stories/${data.storyId}`);
        }, error => {
          console.error('updateStory error: ', error);
          this.generateNotification(
            'Cannot update story. Plase try again',
            'error'
          );
          this.setState({saving: false, creating: false});
        });
      };

      Analytics.track('Story', 'CreateStory', 'Create Story', {
        title: this.state.title
      });
    };
  }

  render() {
    const user = this.data.user;

    const data = this.getData();

    const { characterLimit } = this.state;

    return (
      <section className="story vertical">
        <div className="story-container grid-block">
          <EditableStoryHeader
            onChange={this.handleTitleChange}
            onDrop={this.handleDrop}
            title={data.title}
            imageUrl={data.imageUrl}
            newStory={'yes'}
          />
        </div>
        <div className="new-story-content grid-container">
          <div className="character-limit">
            <div className="grid-block">
              <div className="small-6 grid-content v-align">
                Character Count:
              </div>
              <div className="small-18 grid-content">
                <input
                  type="number"
                  step="10"
                  min="0"
                  value={characterLimit}
                  onChange={this.handleCharacterLimitChange}
                />
              </div>
            </div>
          </div>
          <Labels
            selectedLabels={data.selectedLabels}
            availableLabels={this.data.labels}
            manageLabels={user && user.typeRaw === UserTypeConstants.TEACHER}
            onLabelChange={this.handleLabelChange}
          />
          <div className="new-submission vertical grid-block">
            <PageTextArea
              userId={this.data.user && this.data.user.objectId}
              placeholder="Write the first bit (optional)"
              avatarUrl={UserProfile.getSmallAvatar(user)}
              onChange={this.handleContentChange}
              aids={this.state.aids}
              showHint={this.state.showHint}
              value={data.content}
              initialValue={data.initialValue}
            />
            <div className="button-actions grid-block align-right">
              <div className="small-4 small-offset-1 medium alert hollow button hide">
                Cancel
                <div className="button-spinner alert"></div>
              </div>
              <div className="small-5 medium hollow info button"
                onClick={this.handleWriteAssist} ref="writeAssistBtn"
              >
                <span className={classNames({hide: this.state.assisting})}>
                  {this.state.showHint ? 'Clio On' : 'Clio Off'}
                </span>
                <div className={classNames('button-spinner', 'info', {hide: !this.state.assisting})}></div>
              </div>

              <div
                className={classNames(
                  'counter small-1 small-offset-12 text-center v-align',
                  {'max-reached': data.content ? data.content.trim().length > this.state.characterLimit : false}
                )}
              >
                { data.content ? this.state.characterLimit - data.content.trim().length : this.state.characterLimit }
              </div>
              <div
                className={classNames(
                  'create-button small-4 medium success hollow button',
                  {disabled: data.content && data.content.trim().length > this.state.characterLimit}
                )}
                onClick={this.handleCreate}
              >
                <span className={classNames({hide: this.state.creating})}>Create</span>
                <div className={classNames('button-spinner', 'success', {hide: !this.state.creating})}></div>
              </div>
              <div>
                <NotificationSystem ref="notificationSystem" />
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

NewStory.defaultProps = defaultProps;
NewStory.contextTypes = contextTypes;

export default NewStory;



