import React from 'react';
import ReactDOM from 'react-dom';
import { Parse } from 'parse';
import { Link } from 'react-router';
import $ from 'jquery';
import hello from 'hello';
import NotificationSystem from 'react-notification-system';
import ParseReact from 'parse-react';
import Analytics from '../../support/Analytics';
import ErrorMessages from '../layouts/ErrorMessages';
import ParseAuth from '../../support/ParseAuth';

const contextTypes = {
  router: React.PropTypes.object
};

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.submit = this.submit.bind(this);
    this.keyDown = this.keyDown.bind(this);
    this.clickLogin = this.clickLogin.bind(this);
    this.getEmail = this.getEmail.bind(this);

    this.toggleGoogleLogin = this.toggleGoogleLogin.bind(this);

    this.state = {
      error: null,
      _notificationSystem: null
    };
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;

    if (ParseAuth.loggedIn()) {
      this.context.router.push('/stories');
    } else {
      hello.on('auth.login', (auth) => {
        hello(auth.network).api('/me').then((my) => {
          this.setCurrentUser(my);

          Analytics.identify(Parse.User.current().id, {
            email: my.email,
            first_name: my.first_name,
            last_name: my.last_name,
            user_type: 'student'
          });
        });
      });

      hello.init({
        google: '410488590343-jkvabi78vvd7gejnlpl7bpu1omplgfi4.apps.googleusercontent.com'
      }, {redirect_uri: 'login'});
    }
  }

  render() {
    let errorMessage;
    if (this.state.error) {
      errorMessage = <ErrorMessages messages={[this.state.error]}/>;
    };

    return (
      <form className="session grid-block login-form" onKeyDown={this.keyDown}>
        <div className="grid-container small-18">
          <h1>Login</h1>
          <input ref="email"className="email"type="email"placeholder="your@email.com" />
          <input ref="password"className="password"type="password"placeholder="password" />
          <div className="grid-block align-right">
            <input className="button login-google"type="submit"value="Google Login"onClick={this.toggleGoogleLogin} />
            <input className="button login-password"type="submit"value="Login"onClick={this.clickLogin} />
          </div>
          <div className="grid-block align-right">
            <Link to="/forgot">Forgot my password</Link>
          </div>
          { errorMessage }
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </form>
    );
  }

  getEmail() {
    return ReactDOM.findDOMNode(this.refs.email).value;
  }

  toggleGoogleLogin(e) {
    e.preventDefault();

    hello.login('google', {scope: 'email, publish_files, files'});

    Analytics.track('User', 'Login', 'Google');
  }

  setCurrentUser(user) {
    const { email, thumbnail } = user;

    Parse.Cloud.run('getUserSessionToken', {email: email}).then((result) => {
      return Parse.User.become(result);
    }).then((user) => {
      if (!user.get('smallAvatar') || !user.get('largeAvatar')) {
        return Parse.Cloud.run('fetchAvatarFromUrl', {thumbnail});
      } else {
        return Parse.Promise.as();
      }
    }).then(() => {
      Analytics.login();
      return Parse.User.current().fetch();
    }).then(() => {
      ParseReact.currentUser.update();
      this.context.router.push('/stories');
    }, (e) => {
      this._notificationSystem.addNotification({
        message: 'User not found!',
        level: 'error'
      });
      this._notificationSystem.addNotification({
        message: 'Please contact your administrator.',
        level: 'error'
      });

      this.context.router.push('login');
    });
  }

  submit() {
    const email = this.getEmail();
    const password = ReactDOM.findDOMNode(this.refs.password).value;

    if (!email && !password) {
      this.setState({error: 'Please fill in email and password'});
      return;
    } else if (!email) {
      this.setState({error: 'Please fill in email'});
      return;
    } else if (!password) {
      this.setState({error: 'Please fill in password'});
      return;
    };

    ParseAuth.logIn(email, password, (loggedIn) => {
      if (loggedIn) {
        this.setState({error: null});
        this.context.router.push('/stories');

        Analytics.identify(Parse.User.current().id, {
          email: email,
          user_type: 'student'
        });
        Analytics.login();
      } else {
        this.setState({error: 'Email / Password combination is incorrect'});
      };
    });

    Analytics.track('User', 'Login', 'Email');
  }

  keyDown(e) {
    if (e.keyCode === 13) {
      this.clickLogin(e);
    };
  }

  clickLogin(e) {
    e.preventDefault();
    this.submit();
  }
}

Login.contextTypes = contextTypes;

export default Login;



