import React from 'react';
import moment from 'moment';
import { Parse } from 'parse';
import Isvg from 'react-inlinesvg';
import ParseReact from 'parse-react';
import Avatar from './Avatar';
import UserProfile from '../../support/UserProfile';
import PageRow from '../pages/PageRow';

const ParseComponent = ParseReact.Component(React);

export default class User extends ParseComponent {
  constructor(props) {
    super(props);

    this.countSubmissions = this.countSubmissions.bind(this);
    this.countWeeklyContributions = this.countWeeklyContributions.bind(this);
  }

  observe(props, state) {
    const id = props.params.id;

    const user = new Parse.User();

    user.id = id;

    const notDeletedStoriesQuery = new Parse.Query('Story')
      .notEqualTo('deleted', true);

    const storiesQuery = new Parse.Query('Story')
      .equalTo('creator', user)
      .notEqualTo('deleted', true)
      .descending('createdAt');

    const pagesQuery = new Parse.Query('Page')
      .equalTo('creator', user)
      .descending('createdAt')
      .include('story')
      .matchesQuery('story', notDeletedStoriesQuery);

    const storyLikesQuery = new Parse.Query('Like')
      .equalTo('creator', user)
      .equalTo('typeRaw', 0)
      .matchesQuery('story', storiesQuery);

    const pageLikesQuery = new Parse.Query('Like')
      .equalTo('creator', user)
      .equalTo('typeRaw', 0)
      .matchesQuery('page', pagesQuery);

    const likesQuery = Parse.Query.or(storyLikesQuery, pageLikesQuery);

    return {
      users: new Parse.Query(Parse.User).equalTo('objectId', id),
      stories: storiesQuery,
      pages: pagesQuery,
      likes: likesQuery,
      user: ParseReact.currentUser
    };
  }

  countSubmissions() {
    return this.data.pages.length;
  }

  countWeeklyContributions() {
    return this.data.stories.length;
  }

  handleShowCommentBox(ref) {
    this.data.pages.concat(this.data.stories).forEach(submission => {
      let currentRef = 'CommentBox_' + submission.objectId;
      if (ref !== currentRef) {
        this.refs[currentRef].hideCommentBox();
      }
    });
  }

  render() {
    const user = this.data.users && this.data.users[0];

    const submissions = this.data.stories
      .concat(this.data.pages)
      .sort((a, b) => {
        if (a.createdAt > b.createdAt) {
          return -1;
        } else {
          return 1;
        };
      });

    const pages = submissions.map((submission, index) => {
      let storyName, pageCount, likes, isLiked;

      if (submission.className === 'Story') {
        storyName = submission.title;
        pageCount = submission.submissionCount;
        likes = this.data.likes.filter((like) => {
          return !like.page && like.story && like.story.objectId === submission.objectId;
        });
      } else if (submission.story) {
        storyName = submission.story.title;
        pageCount = submission.story.submissionCount;
      };

      if (submission.className === 'Page') {
        likes = this.data.likes.filter((like) => {
          return like.page && like.page.objectId === submission.objectId;
        });
      };

      isLiked = likes ? !!likes.length : false;

      let ref = 'CommentBox_' + submission.objectId;

      return (
        <div className="grid-container" key={index}>
          <div className="grid-block small-offset-3 small-18">
            <div className="grid-content">
              <p>
                {storyName}
                <span className="reply">
                  <Isvg src={require('../../assets/images/ic_reply_black_24px.svg')} />
                </span>
                {pageCount}
              </p>
            </div>
            <div className="grid-content">
              <p className="text-right">
                {`${moment(submission.createdAt).format('D MMM YYYY')}`}
              </p>
            </div>
          </div>

          <PageRow
            hideAvatar
            hideLine
            hideUsername
            className={submission.className}
            ref={ref}
            onShowCommentsBox={this.handleShowCommentBox.bind(this, ref)}
            isLiked={isLiked}
            {...submission}
            currentUser={this.data.user}
          />
        </div>
      );
    });

    return (
      <section className="user vertical">
        <div className="user-header grid-container">
          <div className="align-center grid-block">
            <div className="grid-block small-6 medium-6">
              <div className="grid-content small-24 medium-24">
                <img
                  className="align-center"
                  src={UserProfile.getLargeAvatar(user)}
                  alt={UserProfile.getDisplayName(user)}
                />
              </div>
            </div>
          </div>
          <div className="grid-block">
            <div className="grid-block vertical">
              <div className="grid-container">
                <div className="grid-content">
                  <p className="stat text-center">
                    {this.countSubmissions()}
                  </p>
                  <p className="text-center">
                    Submissions
                  </p>
                </div>
              </div>
            </div>
            <div className="grid-block vertical">
              <div className="grid-container">
                <div className="grid-content">
                  <p className="stat text-center">
                    {this.countWeeklyContributions()}
                  </p>
                  <p className="text-center">
                    Stories
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="grid-container">
          <div className="grid-container">
            <hr/>
          </div>
        </div>
        <div className="story vertical">
          {pages}
        </div>
      </section>
    );
  }
};



