import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import { Link } from 'react-router';
import classNames from 'classnames';
import Isvg from 'react-inlinesvg';
const ParseComponent = ParseReact.Component(React);
import BookmarkButton from './BookmarkButton';
import { uniq } from 'lodash';

const contextTypes = {
  router: React.PropTypes.object
};

const propTypes = {
  isBookmarked: React.PropTypes.bool
};

const defaultProps = {
  isBookmarked: false
};

class StoryListItem extends ParseComponent {
  shouldComponentUpdate(nextProps, nextState) {
    return nextProps.objectId !== this.props.objectId;
  }

  constructor(props) {
    super(props);

    this.onClickLink = this.onClickLink.bind(this);
    this.onClickTag = this.onClickTag.bind(this);
  }

  observe(props) {
    
    let ids = [];

    if (props.labels) {
      ids = props.labels.map(label => label.objectId);
    } 
    
    let query = new Parse.Query('Label').ascending('typeRaw');
    query.containedIn('objectId', ids);

    return {
      labels: query
    };
  }

  onClickLink(e) {
    e.preventDefault();
    if (this.props.to) {
      this.context.router.push({pathname: this.props.to, query: {id: this.props.objectId}});
      return;
    };
    this.context.router.push(`/stories/${this.props.objectId}`);
  }

  imageContent() {
    if (this.props.image) {
      return (
          <img className="story-image" src={this.props.image._url} />
      );
    }
    return (
      <img className="story-image" src={require('../../assets/images/little_red_riding_hood.jpg')} />
    );
  }

  likesContent() {
    return (
      <span className="likes">
        <Isvg src={require('../../assets/images/ic_favorite_black_24px.svg')} />{this.props.totalLikeCount || 0}
      </span>
    );
  }

  pagesContent() {
    return (
      <span className="pages">
        <Isvg src={require('../../assets/images/ic_reply_black_24px.svg')} />{this.props.pageCount || 0}
      </span>
    );
  }

  tagsContent() {
    var labels;
    if (this.data.labels) {
      labels = this.data.labels.map( (label) => {

        switch(label.typeRaw) {
          case 0:
            return <div className="tags class" key={label.objectId} id={label.objectId} onClick={this.onClickTag}>{label.name}</div>;
          case 1:
            return <div className="tags genre" key={label.objectId} id={label.objectId} onClick={this.onClickTag}>{label.name}</div>;
          case 2:
            return <div className="tags assignment" key={label.objectId} id={label.objectId} onClick={this.onClickTag}>{label.name}</div>;
          case 3:
            return <div className="tags misc" key={label.objectId} id={label.objectId} onClick={this.onClickTag}>{label.name}</div>;
        }
      });  
    }
    return labels;
  }

  onClickTag(e) {
    const labels = e.target.id;
    const existingLabels = this.props.location.query.labels;
    let newLabels;
    if (existingLabels) {
      newLabels = uniq([labels].concat(existingLabels));
    } else {
      newLabels = labels;
    }
    this.context.router.replace({pathname: '/stories/search', query: {labels: newLabels}});
  }

  render() {
    return (
      <div className="story-list-item vertical grid-block">
        <header className="grid-content square-aspect-ratio">
          <div className="content" onClick={this.onClickLink}>
            {this.imageContent()}
            <div className="gradient-overlay"/>
            <h1>{this.props.title}</h1>
          </div>
        </header>
        <footer className="grid-block vertical">
          <div className="grid-block">
            <div className="grid-block small-10">
              <BookmarkButton {...this.props} />
            </div>
            <div className="grid-block small-14 align-right">
              {this.likesContent()}
              {this.pagesContent()}
            </div>
          </div>
          <div className="footer-tags">
            {this.tagsContent()}
          </div>
        </footer>
      </div>
    );
  }
}

StoryListItem.contextTypes = contextTypes;
StoryListItem.propTypes = propTypes;
StoryListItem.defaultProps = defaultProps;

export default StoryListItem;



