import React from 'react';
import { Link } from 'react-router';
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import NotificationCenter from './NotificationCenter';

export default class UserPanelNavigationBar extends React.Component {
  render() {
    return (
      <TitleBar>
        <TitleBar.Left>
          <Link to="/">
            <img src={require('../../assets/images/storyappylogo.svg')} />
          </Link>
        </TitleBar.Left>
        <TitleBar.Center>
          User Panel
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            <div className="grid-block">
              <NotificationCenter />
            </div>
            <div className="grid-block">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
      </TitleBar>
    );
  }
}



