import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import Notification from './Notification';

export default class Notifications extends ParseComponent {

  constructor(props) {
    super(props);
    this.data = {
      activities: props.activities
    };
  }

  observe(props) {
    return {
      activities: new Parse.Query('Activity').equalTo('toUser', Parse.User.current()).include('fromUser').include('story').descending('createdAt'),
      user: ParseReact.currentUser
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.badgeCount != this.props.badgeCount) {
      this.refreshQueries('activities');
    }
  }

  render() {
    let activities, loadingContainer, loadingContent;

    if (this.pendingQueries().length) {
      loadingContent = (
        <div className="button-spinner-wrapper">
          <div className="button-spinner"></div>
        </div>
      );
    } else if (this.data.activities.length == 0) {
      loadingContent = 'No Activity';
    }

    loadingContainer = (
      <ul className="activities sub-menu text-left">
        <li className="menu-item activity-item">
          <div className="grid-block">
            <div className="grid-content text-center">
              {loadingContent}
            </div>
          </div>
        </li>
      </ul>
    );

    if (this.data.activities.length > 0) {
      return (
        <ul className="activities sub-menu text-left">
          {this.data.activities.map((activity) => {
            return (
              <Notification {...activity}
                            key={activity.objectId} />
            );
          })}
        </ul>
      );
    } else {
      return loadingContainer;
    }

  }
}

Notifications.defaultProps = {
  activities: []
};



