import React from 'react';

const Label = class Label extends React.Component {
  constructor(props) {
    super(props);

    this.blockEvent = this.blockEvent.bind(this);
    this.handleOnRemove = this.handleOnRemove.bind(this);
  }
  blockEvent(event) {
    event.stopPropagation();
  }

  handleOnRemove(event) {
    event.preventDefault();
    event.stopPropagation();
    if (!this.props.disabled) {
      this.props.onRemove(this.props.value);
    }
  }

  render() {

    let label;

    if(!this.props.onRemove && !this.props.optionLabelClick) {
      return <div className="Select-value">{this.props.children}</div>;
    }

    if (this.props.value.type == 'label') {
      
      switch(this.props.value.typeRaw) {

        case 0:
        label = <div className="select-item-claass">
                <span className="select-label">{this.props.children}</span>
                <span className="select-icon"
                  onMouseDown={this.blockEvent}
                  onClick={this.handleOnRemove}
                  onTouchEnd={this.handleOnRemove}>&times;</span>
              </div>;
        break;

        case 1:
        label = <div className="select-item-genre">
                <span className="select-label">{this.props.children}</span>
                <span className="select-icon"
                  onMouseDown={this.blockEvent}
                  onClick={this.handleOnRemove}
                  onTouchEnd={this.handleOnRemove}>&times;</span>
              </div>;
        break;

        case 2:
        label = <div className="select-item-assignment">
                  <span className="select-label">{this.props.children}</span>
                  <span className="select-icon"
                    onMouseDown={this.blockEvent}
                    onClick={this.handleOnRemove}
                    onTouchEnd={this.handleOnRemove}>&times;</span>
                </div>;
          break;
        
        case 3:
        label = <div className="select-item-misc">
                  <span className="select-label">{this.props.children}</span>
                  <span className="select-icon"
                    onMouseDown={this.blockEvent}
                    onClick={this.handleOnRemove}
                    onTouchEnd={this.handleOnRemove}>&times;</span>
                </div>;
          break;

        default:
        break;
      }
    } else if (this.props.value.type == 'story') {
      label = <div className="select-item-story">
                <span className="select-label">{this.props.children}</span>
                <span className="select-icon"
                  onMouseDown={this.blockEvent}
                  onClick={this.handleOnRemove}
                  onTouchEnd={this.handleOnRemove}>&times;</span>
              </div>;
    } else {
      label = <div className="select-item">
                <span className="select-label">{this.props.children}</span>
                <span className="select-icon"
                  onMouseDown={this.blockEvent}
                  onClick={this.handleOnRemove}
                  onTouchEnd={this.handleOnRemove}>&times;</span>
              </div>;
    }

    return (
      label
    );
  }
};

Label.propTypes = {
  disabled: React.PropTypes.bool,                   // disabled prop passed to ReactSelect
  onOptionLabelClick: React.PropTypes.func,         // method to handle click on value label
  onRemove: React.PropTypes.func,                   // method to handle remove of that value
  value: React.PropTypes.object.isRequired,                   // option passed to component
  optionLabelClick: React.PropTypes.bool,           // indicates if onOptionLabelClick should be handled
};

export default Label;


