import React from 'react';
import _ from 'lodash';
import classNames from 'classnames';
import UserTypeConstants from '../../constants/UserTypeConstants';

export default class NewUsersTable extends React.Component {
  render() {
    const classOptions = this.props.classes && this.props.classes.map((c) => {
      return (<option key={c.objectId} value={c.objectId}>{c.name}</option>);
    });

    const rows = _.range(this.props.rowLength).map((index) => {
      const [email, displayName, password, classLabel] = this.props.rowGetter(index);
      const errors = this.props.errors[index] && this.props.errors[index].map((e) => {
        return (
          <div className="error-message">{e}</div>
        );
      });

      return (
        <tr key={index}>
          <td>
            {index + 1}
          </td>
          <td>
            {errors && errors[0]}
            <input
              type="text"
              placeholder="email"
              value={email}
              className={classNames({error: errors && errors[0]})}
              onChange={this.props.onChangeInput && this.props.onChangeInput.bind(this, index, 'email')}
            />
          </td>
          <td>
            {errors && errors[1]}
            <input
              type="text"
              placeholder="username"
              value={displayName}
              className={classNames({error: errors && errors[1]})}
              onChange={this.props.onChangeInput && this.props.onChangeInput.bind(this, index, 'displayName')}
            />
          </td>
          <td>
            {errors && errors[2]}
            <input
              type="text"
              placeholder="password"
              value={password}
              className={classNames({error: errors && errors[2]})}
              onChange={this.props.onChangeInput && this.props.onChangeInput.bind(this, index, 'password')}
            />
          </td>
          <td>
            {errors && errors[3]}
            <select
              name="class"
              defaultValue=""
              onChange={this.props.onChangeInput && this.props.onChangeInput.bind(this, index, 'classLabel')}
            >
              <option value=""> -- select a class -- </option>
              {classOptions}
            </select>
          </td>
          <td>
            {errors && errors[3]}
            <select
              name="typeRaw"
              defaultValue={UserTypeConstants.STUDENT}
              onChange={this.props.onChangeInput && this.props.onChangeInput.bind(this, index, 'typeRaw')}
            >
              <option value={UserTypeConstants.STUDENT}>Student</option>
              <option value={UserTypeConstants.TEACHER}>Teacher</option>
            </select>
          </td>
          <td>
            <div className="remove">
              <a onClick={this.props.onRemove.bind(this, index)}>
                <img src={require('../../assets/images/Cancel.png')}/>
              </a>
            </div>
          </td>
        </tr>
      );
    });

    return (
      <table>
        <thead>
          <tr>
            <th/>
            <th>Email</th>
            <th>Username</th>
            <th>Password</th>
            <th>Class</th>
            <th>Type</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {rows}
        </tbody>
      </table>
    );
  }
}



