import MediumEditor from 'medium-editor';
import rangy from 'rangy';
import 'rangy/lib/rangy-classapplier';

rangy.init();

global.MediumEditor = MediumEditor;

const HighlightButton = MediumEditor.extensions.form.extend({
  name: 'highlighter',
  contentDefault: '<b>H</b>',
  contentFA: '<i class="fa fa-paint-brush"></i>',
  aria: 'Highlight',
  placeholderText: 'Enter your comment',
  action: 'Highlight',
  init() {
    MediumEditor.extensions.form.prototype.init.call(this, arguments);
  },

  handleClick(event) {
    event.preventDefault();
    event.stopPropagation();
    const comment = this.getComment();
    const applier = this.getApplier();
    if (!applier.isAppliedToSelection()) {
      applier.applyToSelection();
    }

    const opts = {
      content: comment
    };

    if (!this.isDisplayed()) {
      this.showForm(opts);
    }

    return false;
  },

  getForm() {
    if (!this.form) {
      this.form = this.createForm();
    }
    return this.form;
  },

  getComment() {
    const element = this.base.getSelectedParentElement();
    return element.getAttribute('data-hint');
  },

  getApplier() {
    const options = {
      elementAttributes: {
        'data-category': 'Comment from teacher',
        'data-report': 'teacher'
      }
    };
    const comment = this.getComment();
    if (comment) {
      options['elementAttributes'] = {
        'data-hint': comment
      };
    }

    return rangy.createClassApplier('highlight', options);
  },

  getTemplate() {
    const template = [
      `<textarea rows=10 cols=30 class="medium-editor-toolbar-input" placeholder="${this.placeholderText}"></textarea>`
    ];
    template.push(
      `
      <a href="#" class="medium-editor-toolbar-save">
        ${this.getEditorOption('buttonLabels') === 'fontawesome' ? '<i class="fa fa-check"></i>' : this.formSaveLabel}
      </a>
      `
    );
    template.push(
      `
      <a href="#" class="medium-editor-toolbar-close">
        ${this.getEditorOption('buttonLabels') === 'fontawesome' ? '<i class="fa fa-times"></i>' : this.formSaveLabel}
      </a>
      `
    );
    template.push(
      `
      <a href="#" class="medium-editor-toolbar-trash">
        ${this.getEditorOption('buttonLabels') === 'fontawesome' ? '<i class="fa fa-trash"></i>' : this.formSaveLabel}
      </a>
      `
    );
    return template.join('');
  },

  isDisplayed() {
    return this.getForm().style.display === 'block';
  },

  hideForm() {
    this.getForm().style.display = 'none';
    this.getInput().value = '';
  },

  showForm(opts) {
    const input = this.getInput();
    const options = opts || { content: '' };

    this.base.saveSelection();
    this.hideToolbarDefaultActions();
    this.getForm().style.display = 'block';
    this.setToolbarPosition();

    input.value = options.content || '';
    input.focus();
  },

  destroy() {
    if (!this.form) {
      return false;
    }

    if (this.form.parentNode) {
      this.form.parentNode.removeChild(this.form);
    }

    delete this.form;
  },

  getFormOpts() {
    return {
      content: this.getInput().value.trim() || '',
    };
  },

  doFormSave() {
    const opts = this.getFormOpts();
    this.completeFormSave(opts);
  },

  completeFormSave(opts) {
    this.base.restoreSelection();
    this.execAction(this.action, opts);
    this.base.checkSelection();
    const node = this.base.getSelectedParentElement();
    node.setAttribute('data-hint', opts.content);
  },

  doFormCancel() {
    this.base.restoreSelection();
    this.base.checkSelection();
    if (!this.getComment()) {
      this.getApplier().undoToSelection();
    }
  },

  doFormTrash() {
    this.base.restoreSelection();
    this.base.checkSelection();
    this.getApplier().undoToSelection();
  },

  attachFormEvents(form) {
    const close = form.querySelector('.medium-editor-toolbar-close');
    const save = form.querySelector('.medium-editor-toolbar-save');
    const input = form.querySelector('.medium-editor-toolbar-input');
    const trash = form.querySelector('.medium-editor-toolbar-trash');

    this.on(form, 'click', this.handleFormClick.bind(this));
    this.on(input, 'keyup', this.handleTextboxKeyup.bind(this));
    this.on(close, 'click', this.handleCloseClick.bind(this));
    this.on(save, 'click', this.handleSaveClick.bind(this));
    this.on(trash, 'click', this.handleTrashClick.bind(this));
  },

  createForm() {
    const doc = this.document;
    const form = doc.createElement('div');

    form.className = 'medium-editor-toolbar-form';
    form.id = 'medium-editor-toolbar-form-anchor-' + this.getEditorId();
    form.innerHTML = this.getTemplate();
    this.attachFormEvents(form);

    return form;
  },

  getInput() {
    return this.getForm().querySelector('textarea.medium-editor-toolbar-input');
  },

  handleTextboxKeyup(event) {
    if (event.keyCode === MediumEditor.util.keyCode.ESCAPE) {
      event.preventDefault();
      this.doFormCancel();
    }
  },

  handleFormClick(event) {
    event.stopPropagation();
  },

  handleSaveClick(event) {
    event.preventDefault();
    this.doFormSave();
  },

  handleCloseClick(event) {
    event.preventDefault();
    this.doFormCancel();
  },

  handleTrashClick(event) {
    event.preventDefault();
    this.doFormTrash();
  }
});

export default HighlightButton;



