import React from 'react';
import { Link } from 'react-router';
import UserStatusConstants from '../../constants/UserStatusConstants';

export default class UserPanelTable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedAll: false,
      selectedRows: [],
    };

    this.handleSelectAll = this.handleSelectAll.bind(this);
  }

  handleSelectAll(e) {
    let rows;
    if (this.state.selectedAll) {
      rows = [];
      this.setState({selectedAll: false, selectedRows: rows});
    } else {
      rows = _.range(this.props.rowLength);
      this.setState({
        selectedAll: true,
        selectedRows: rows,
      });
    };
    this.props.onChangeSelectedRows && this.props.onChangeSelectedRows(rows);
  }

  handleSelectRow(index) {
    const indexInSelectedRows = this.state.selectedRows.indexOf(index);
    let rows = this.state.selectedRows;

    if (indexInSelectedRows !== -1) {
      _.pullAt(rows, indexInSelectedRows);
      this.setState({selectedAll: false, selectedRows: rows});
    } else {
      rows = this.state.selectedRows.concat(index);
      this.setState({selectedRows: rows});
    };

    this.props.onChangeSelectedRows && this.props.onChangeSelectedRows(rows);
  }

  render() {
    const rows = this.props.rowGetter && _.range(this.props.rowLength).map((index) => {
      const data = this.props.rowGetter(index);
      const [email, firstName, lastName, username, classLabel, statusRaw, objectId] = data;
      let emailField,
        firstNameField,
        lastNameField,
        usernameField,
        classField,
        statusField,
        checkbox;
      if (this.props.isEditing) {
        checkbox = (
          <div>
            <input
              id={`checkbox-${index}`}
              type="checkbox"
              checked={this.state.selectedRows.indexOf(index) !== -1}
              onChange={this.handleSelectRow.bind(this, index)}
            />
            <label htmlFor={`checkbox-${index}`}></label>
          </div>
        );
      };
      if (this.props.isEditing && this.state.selectedRows.indexOf(index) !== -1) {
        emailField = (
          <input
            type="text" value={email}
            onChange={this.props.onChangeInput.bind(this, index, 'email')}
          />
        );
        firstNameField = (
          <input
            type="text" value={firstName}
            onChange={this.props.onChangeInput.bind(this, index, 'firstName')}
          />
        );
        lastNameField = (
          <input
            type="text" value={lastName}
            onChange={this.props.onChangeInput.bind(this, index, 'lastName')}
          />
        );
        usernameField = (
          <input
            type="text" value={username}
            onChange={this.props.onChangeInput.bind(this, index, 'username')}
          />
        );
        const classOptions = this.props.classes && this.props.classes.map((c) => {
          return (<option key={c.objectId} value={c.objectId}>{c.name}</option>);
        });
        classField = (
          <select
            name="class"
            defaultValue=""
            value={classLabel}
            onChange={this.props.onChangeInput.bind(this, index, 'classLabel')}
          >
            <option value="" disabled> -- select a class -- </option>
            {classOptions}
          </select>
        );
        statusField = (
          <select
            name="statusRaw"
            defaultValue=""
            value={statusRaw}
            onChange={this.props.onChangeInput.bind(this, index, 'statusRaw')}
          >
            <option value={UserStatusConstants.ACTIVE}>Active</option>
            <option value={UserStatusConstants.INACTIVE}>Inactive</option>
          </select>
        );
      } else {
        emailField = email;
        firstNameField = firstName;
        lastNameField = lastName;
        usernameField = (
          <Link to={`/users/${objectId}/stats/`}>
            {username}
          </Link>
        );
        classField = _.chain(this.props.classes)
          .find((c) => c.objectId === classLabel)
          .result('name')
          .value();
        statusField = statusRaw === 0 ? 'Active' : 'Inactive';
      };
      return (
        <tr key={index}>
          <td>
            {checkbox}
          </td>
          <td>{usernameField}</td>
          <td>{firstNameField}</td>
          <td>{lastNameField}</td>
          <td>{emailField}</td>
          <td>{classField}</td>
        </tr>
      );
    });

    let checkbox;

    if (this.props.isEditing) {
      checkbox = (
        <div>
          <input
            id="select-all"
            type="checkbox"
            checked={this.state.selectedAll}
            onChange={this.handleSelectAll}
          />
          <label htmlFor="select-all"></label>
        </div>
      );
    };
    return (
      <table>
        <thead>
          <tr>
            <th>
              {checkbox}
            </th>
            <th>Username</th>
            <th>First Name</th>
            <th>Last Name</th>
            <th>Email</th>
            <th>Class</th>
          </tr>
        </thead>
        <tbody>
          {rows}
        </tbody>
      </table>
    );
  }
}



