import slug from 'slug';
import React from 'react';
import SelectLabels from './SelectLabels';
import { Link } from 'react-router';
import LabelTypeConstants from '../../constants/LabelTypeConstants';

function toOptions (labels) {
  return labels.map((label) => {
    return {
      label: label.name,
      value: slug(label.name),
    };
  });
}

function getOptions(labels) {
  return _(labels).groupBy('typeRaw')
    .mapValues(toOptions)
    .value();
}

export default class Labels extends React.Component {
  constructor(props) {
    super(props);

    this.handleLabelChange = this.handleLabelChange.bind(this);
  }

  handleLabelChange(type, options) {
    const selectedLabels = options.map((option) => {
      return this.props.availableLabels.find((label) => {
        return (slug(label.name) === option.value) && (label.typeRaw === type);
      });
    });

    this.props.onLabelChange(type, selectedLabels);
  }

  renderManageLabels() {
    return (
      <div className="grid-block small-5 medium-5 large-5">
        <div className="grid-content">
          <Link to="/admin/labels" className="manage-button button hollow right">Manage Labels</Link>
        </div>
      </div>
    );
  }

  render() {
    const ids = this.props.selectedLabels && this.props.selectedLabels.map((label) => label && label.objectId);
    const groupedOptions = getOptions(this.props.availableLabels);
    const selectedOptions = getOptions(this.props.selectedLabels);

    let teacherComponents;
    if (this.props.manageLabels) {
      teacherComponents = this.renderManageLabels();
    }

    return (
      <div className="labels">
        <div className="grid-block">
          <div className="grid-block vertical">
            <SelectLabels
              name="Class"
              value={selectedOptions[LabelTypeConstants.CLASS]}
              options={groupedOptions[LabelTypeConstants.CLASS]}
              onChange={this.handleLabelChange.bind(this, LabelTypeConstants.CLASS)}
            />
            <SelectLabels
              name="Genre"
              value={selectedOptions[LabelTypeConstants.GENRE]}
              options={groupedOptions[LabelTypeConstants.GENRE]}
              onChange={this.handleLabelChange.bind(this, LabelTypeConstants.GENRE)}
            />
            <SelectLabels
              name="Assignment"
              value={selectedOptions[LabelTypeConstants.ASSIGNMENT]}
              options={groupedOptions[LabelTypeConstants.ASSIGNMENT]}
              onChange={this.handleLabelChange.bind(this, LabelTypeConstants.ASSIGNMENT)}
            />
            <SelectLabels
              name="Misc"
              value={selectedOptions[LabelTypeConstants.MISC]}
              options={groupedOptions[LabelTypeConstants.MISC]}
              onChange={this.handleLabelChange.bind(this, LabelTypeConstants.MISC)}
            />
          </div>
          {teacherComponents}
        </div>
      </div>
    );
  }
}


