import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';
import pubnub from 'pubnub';

import Notifications from './Notifications';
import ParseAuth from '../../support/ParseAuth';
import Analytics from '../../support/Analytics';
import secrets from '../../../config/secrets';

export default class NotificationCenter extends ParseComponent {

  constructor(props) {
    super(props);
    this.state = {
      isSubscribing: false,
      isShowing: false,
      count: 0
    };
    this.data = {
      user: props.user,
      channels: props.channels
    };

    this.toggleShow = this.toggleShow.bind(this);
    this.toggleHide = this.toggleHide.bind(this);
  }

  observe(props) {
    let observed = {
      user: ParseReact.currentUser,
    };
    if (Parse.User.current()) {
      observed.channels = Parse.User.current().relation('channels').query();
    };
    return observed;
  }

  componentDidMount() {
    this.sub = pubnub({
      subscribe_key: secrets.pubnubSub
    });
  }

  componentWillUnmount() {
    super.componentWillUnmount();
    this._unsubscribeChannel();
  }

  _subscribeChannel(user, channels) {
    if (this.state.isSubscribing) return;

    let channelGroup = user.objectId;
    this.names = channels.map((c) => {
      return c.name;
    });
    this.sub.subscribe({
      channel: this.names,
      connect: () => {
        this.setState({isSubscribing: true, count: user.badge});
      },
      message: (message) => {
        if (message && message.aps) {
          this.setState({count: message.aps.badge});
        } else {
          this.incrementBadge();
        }
        if (this.state.isShowing) {
          this._resetBadge();
        }
      }
    });
  }

  _unsubscribeChannel() {
    this.setState({isSubscribing: false});
    this.sub.unsubscribe({
      channel: this.names
    });
  }

  incrementBadge() {
    this.setState({count: this.state.count + 1});
  }

  toggleShow() {
    this.setState({isShowing: true});
    this._resetBadge();
    document.addEventListener('click', this.toggleHide);

    Analytics.track('User', 'NotificationCenter', 'Show Activity');
  }

  toggleHide() {
    this.setState({isShowing: false});
    document.removeEventListener('click', this.toggleHide);
  }

  _resetBadge() {
    ParseReact.Mutation.Set(this.data.user, {
      badge: 0
    }).dispatch().then(() => {
      this.setState({count: 0});
    });
  }

  renderBadge() {
    if (!this.state.isShowing && this.state.count > 0) {
      return <span className="alert badge">{this.state.count}</span>;
    }
  }

  renderActivities() {
    if (this.state.isShowing) {
      return (
        <Notifications badgeCount={this.state.count} />
      );
    } else {
      return null;
    }
  }

  render() {
    if (this.pendingQueries.length > 0) {

    } else if (this.data.user && this.data.channels.length > 0) {
      this._subscribeChannel(this.data.user, this.data.channels);
    }

    return (
      <div>
        <div className="drop-down-menu" onClick={this.toggleShow}>
          {this.renderBadge()}
          <img src={require('../../assets/images/ic_add_alert_black_24px.svg')} />
          {this.renderActivities()}
        </div>
      </div>
    );
  }

}

NotificationCenter.defaultProps = {
  user: {},
  channels: []
};



