import { List, Repeat } from 'immutable';
import _ from 'lodash';

import {
  BlockMapBuilder,
  SelectionState,
  CharacterMetadata,
  ContentBlock,
  EditorState,
  Entity,
  Modifier,
  genKey
} from 'draft-js';

function getRanges(tags) {
  const ranges = _(tags)
    .chain()
    .reduce((sum, tag) => {
      return [
        ...sum,
        {position: tag.startPos, type: 'start'},
        {position: tag.endPos, type: 'end'}
      ];
    }, [])
    .uniqWith(_.isEqual)
    .orderBy(['position', 'type'], ['asc', 'desc'])
    .reduce((sum, node, i, nodes) => {
      if (i < nodes.length - 1) {
        const nextNode = nodes[i + 1];
        const startPos = node.type === 'start' ? node.position : node.position + 1;
        const endPos = nextNode.type === 'start' ? nextNode.position - 1 : nextNode.position;
        return [...sum, {startPos, endPos}];
      } else {
        return sum;
      }
    }, [])
    .value();

  return ranges.map(range => {
    return {...range, tags: tags.filter(t => isOverlapped(t, range))};
  });
}

function getBlocksWithPosition(contentState) {
  return contentState
    .getBlocksAsArray()
    .reduce((array, block, i, blocks) => {
      const text = block.getText();
      const length = i < blocks.length - 1 ? text.length + 1 : text.length;
      const startPos = array.slice(0, i).reduce((sum, a) => sum + a.length, 0);
      const endPos = startPos + length - 1;
      return [...array, {block, length, startPos, endPos}];
    }, []);
}

function isOverlapped(tag1, tag2) {
  return Math.max(tag1.startPos, tag2.startPos) <= Math.min(tag1.endPos, tag2.endPos);
}

function groupTags(tags) {
  return tags.reduce((sum, tag, i, tags) => {
    const index = sum.findIndex(t => isOverlapped(t, tag));
    if (index !== -1) {
      return sum.map((t, i) => {
        if (index === i) {
          return {
            startPos: Math.min(t.startPos, tag.startPos),
            endPos: Math.max(t.endPos, tag.endPos),
            tags: [...t.tags, tag]
          };
        }
        return t;
      });
    } else {
      return [
        ...sum,
        {
          startPos: tag.startPos,
          endPos: tag.endPos,
          tags: [tag]
        }
      ];
    }
  }, []);
}

function getBlockAtPosition(position, blocks) {
  return blocks.find(b => b.startPos <= position && position <= b.endPos);
}

function mapTagsToRanges(tags) {
  const groupedTags = groupTags(tags);
  return _.flatMap(groupedTags, t => getRanges(t.tags));
}

function addRangeToContentState(range, contentState) {
  const entityKey = Entity.create('AID', 'MUTABLE', {tags: range.tags});

  const blocksWithPosition = getBlocksWithPosition(contentState);
  const startBlock = blocksWithPosition.find(b => b.startPos <= range.startPos && range.startPos <= b.endPos);
  const endBlock = blocksWithPosition.find(b => b.startPos <= range.endPos && range.endPos <= b.endPos);

  const selectionState = SelectionState.createEmpty('aid').merge({
    anchorKey: startBlock.block.getKey(),
    anchorOffset: range.startPos - startBlock.startPos,
    focusKey: endBlock.block.getKey(),
    focusOffset: range.endPos - startBlock.startPos + 1
  });

  return Modifier.applyEntity(
    contentState,
    selectionState,
    entityKey
  );
}

export function insertAidBlocks(editorState, tags) {
  const ranges = mapTagsToRanges(tags);
  const selectionState = editorState.getSelection();
  const withTags = ranges.reduce((contentState, r) => {
    return addRangeToContentState(r, contentState);
  }, editorState.getCurrentContent());
  const newContent = withTags.merge({
    selectionBefore: selectionState,
    selectionAfter: selectionState
  });
  return EditorState.push(editorState, newContent, 'apply-aid');
}

export function sliceTextWithTags(text, tags) {
  return mapTagsToRanges(tags).reduce((sum, r, i, ranges) => {
    const { startPos, endPos, tags } = r;
    const chunk = text.slice(startPos, endPos + 1);
    let result = sum;
    let prev = [];
    let end = [];
    if (i === 0 && startPos !== 0) {
      prev.push({text: text.slice(0, startPos), tags: []});
    }
    if (i !== 0 && i <= ranges.length - 1) {
      const { endPos: previousEnd } = ranges[i - 1];
      if (startPos !== previousEnd + 1) {
        prev.push({text: text.slice(previousEnd + 1, startPos), tags: []});
      }
    }
    if (i === ranges.length - 1) {
      if (endPos !== text.length - 1) {
        end.push({text: text.slice(endPos + 1), tags: []});
      }
    }
    return [...result, ...prev, {text: chunk, tags}, ...end];
  }, []);
}



