import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import NotificationSystem from 'react-notification-system';
import classNames from 'classnames';

import Analytics from '../../support/Analytics';
import PageStatusConstants from '../../constants/PageStatusConstants';
import PageTextArea from './PageTextArea';
import UserProfile from '../../support/UserProfile';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class NewSubmission extends ParseComponent {
  constructor(props) {
    super(props);

    var story = new Parse.Object('Story');
    story.id = props.storyId;

    this.state = {
      storyId: props.storyId,
      story: story,
      refPageId: props.refPageId,
      creator: props.creator,
      userInput: null,
      showHint: false,
      aids: null,
      saving: false,
      submitting: false,
      assisting: false,
      modified: false,
    };

    this.validate = this.validate.bind(this);

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleSaveDraft = this.handleSaveDraft.bind(this);
    this.handleWriteAssist = this.handleWriteAssist.bind(this);
    this.routerWillLeave = this.routerWillLeave.bind(this);
    this.saveDraft = this.saveDraft.bind(this);
  }

  componentDidMount() {
    const { route } = this.props;
    const { router } = this.context;
    router.setRouteLeaveHook(route, this.routerWillLeave);
    this._notificationSystem = this.refs.notificationSystem;
  }

  observe(props) {
    var story = new Parse.Object('Story');
    story.id = props.storyId;

    return {
      user: ParseReact.currentUser,
      drafts: new Parse.Query('Page')
            .equalTo('story', story)
            .equalTo('statusRaw', PageStatusConstants.DRAFTED)
            .equalTo('creator', Parse.User.current())
            .ascending('createdAt')
            .include('creator')
    };
  }

  routerWillLeave(nextLocation) {
    this.saveDraft();
  }

  saveDraft() {
    let content = this._gatherData();
    let pageStatus = PageStatusConstants.DRAFTED;

    if (!content) return;

    if (this.validate()) {
      let promise;
      if (this.data.drafts.length) {
        ParseReact.Mutation.Set(
          this.data.drafts[0], {content: content}
        ).dispatch();
      } else {
        ParseReact.Mutation.Create('Page', {
          content: content,
          statusRaw: pageStatus,
          refPageId: this.state.refPageId,
          story: this.state.story,
          likeCount: 0,
          creator: Parse.User.current()
        }).dispatch();
      }
    }
  }

  handleSaveDraft(e) {
    e.preventDefault();
    let content = this._gatherData();

    if (!content) return;

    if (this.validate()) {
      this.setState({saving: true});
      let promise;

      if (this.data.drafts.length) {
        promise = this._updateToParse(content)
          .then(() => {
            this.setState({saving: false});
          });
      } else {
        promise = this._saveNewToParse(content, PageStatusConstants.DRAFTED)
          .then(() => {
            let state = {
              submitting: false,
              saving: false,
            };

            if (pageStatus !== PageStatusConstants.DRAFTED) {
              state.userInput = '';
            }

            this.setState({...state});
          });
      }

      promise.then(() => {
        this.refs.notificationSystem.addNotification({
          message: 'Draft saved!',
          level: 'success',
        });
      });

      Analytics.track('Page', 'SaveDraftPage', 'NewSubmission', {
        refPageId: this.state.refPageId,
        saveAgain: this.data.drafts.length > 0
      });
    };
  }

  validate() {
    if (this.state.userInput && this.state.userInput.trim().length > this.props.characterLimit) {
      this.refs.notificationSystem.addNotification({
        message: `Your story content must be less than ${this.props.characterLimit} characters!`,
        level: 'error'
      });
      return false;
    } else {
      return true;
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    if (this.validate()) {
      const content = this._gatherData();
      this.setState({submitting: true});
      this.refs['textArea'].clear();
      if (this.props.editingPage) {
        ParseReact.Mutation.Set(this.props.editingPage, {
          content
        }).dispatch()
          .then(() => {
            this.refs.notificationSystem.addNotification({
              message: 'Successfully edited!',
              level: 'success'
            });
            this.setState({submitting: false});
            this.props.finishSubmit();
          });
        Analytics.track('Page', 'SubmitPage', 'EditPage', {
          refPageId: this.props.editingPage.objectId
        });
        return;
      }
      this._deleteFromParse();
      this._saveNewToParse(content, PageStatusConstants.PENDING)
        .then(() => {
        this.refs.notificationSystem.addNotification({
          message: 'Successfully submitted!',
          level: 'success'
        });
        this.props.finishSubmit();
      });

      Analytics.track('Page', 'SubmitPage', 'NewSubmission', {
        refPageId: this.state.refPageId,
      });
    };
  }

  handleChange(text) {
    this.setState({userInput: text, modified: true});
  }

  handleWriteAssist(e) {
    let assisting = false;
    if (!this.state.showHint && this.state.userInput && this.state.userInput.length) {
      assisting = true;
      Parse.Cloud.run('getAid', {text: this.state.userInput}, (response) => {
        this.setState({aids: JSON.parse(response.text), showHint: true, assisting: false});
      });

      Analytics.track('Page', 'Clio', 'On', {
        refPageId: this.state.refPageId,
      });
    };
    this.setState({
      assisting,
      showHint: !this.state.showHint,
    });
  }

  _gatherData() {
    let content = this.state.userInput && this.state.userInput.trim();
    if (!content) {
      return;
    }

    return content;
  }

  _deleteFromParse() {
    if (this.data.drafts.length) {
      ParseReact.Mutation.Destroy(this.data.drafts[0]).dispatch();
    }
  }

  _updateToParse(content) {
    return ParseReact.Mutation.Set(
      this.data.drafts[0], {content: content}
    ).dispatch().then(() => {
      this.setState({saving: false});
    });
  }

  _saveNewToParse(content, pageStatus) {
    return ParseReact.Mutation.Create('Page', {
      content: content,
      statusRaw: pageStatus,
      refPageId: this.state.refPageId,
      story: this.state.story,
      likeCount: 0,
      creator: Parse.User.current()
    }).dispatch().then(() => {
      let state = {
        submitting: false,
        saving: false,
      };

      if (pageStatus !== PageStatusConstants.DRAFTED) {
        state.userInput = '';
      }

      this.setState({...state});
    });
  }

  render() {
    const user = this.data.user;

    let textAreaValue, initialValue;

    if (!this.state.modified) {
      if (this.data.drafts && this.data.drafts.length) {
        initialValue = this.data.drafts[0].content;
      } else if (this.props.editingPage) {
        textAreaValue = this.props.editingPage.content;
      }
    } else {
      textAreaValue = this.state.userInput;
    };

    return (
      <div className="new-submission small-24 large-24 vertical grid-block">
        <PageTextArea
          ref="textArea"
          onChange={this.handleChange}
          initialValue={initialValue}
          value={textAreaValue}
          userId={user && user.objectId}
          avatarUrl={UserProfile.getSmallAvatar(user)}
          title={UserProfile.getDisplayName(user)}
          aids={this.state.aids}
          placeholder="What happens next?"
          showHint={this.state.showHint}
        />
        <div className="button-actions grid-block align-right">
          <div className="small-5 medium hollow info button"
            onClick={this.handleWriteAssist} ref="writeAssistBtn">
              <span className={classNames({hide: this.state.assisting})}>
                { this.state.showHint ? 'Clio On' : 'Clio Off' }
              </span>
              <div className={classNames('button-spinner', 'info', {hide: !this.state.assisting})}></div>
          </div>

          <div
            className={classNames(
              'counter small-1 small-offset-12 text-center v-align',
              {'max-reached': this.state.userInput ? this.state.userInput.trim().length > this.props.characterLimit : false}
            )}
          >
            { this.state.userInput ? this.props.characterLimit - this.state.userInput.trim().length : this.props.characterLimit }
          </div>

          <div
            className={classNames(
              'small-4 medium success hollow button',
              {disabled: this.state.userInput ? this.state.userInput.trim().length > this.props.characterLimit : false}
            )}
            onClick={this.handleSubmit} ref="submitBtn">
              <span className={classNames({hide: this.state.submitting})}>Submit</span>
              <div className={classNames('button-spinner', 'success', {hide: !this.state.submitting})}></div>
            </div>
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </div>
    );
  }
}

NewSubmission.contextTypes = contextTypes;

export default NewSubmission;




