import React from 'react';
import { DraftEditorBlock, Entity } from 'draft-js';
import _ from 'lodash';
import classNames from 'classnames';

function getRanges(tags) {
  return _(tags)
    .chain()
    .reduce((sum, tag) => {
      return [
        ...sum,
        {position: tag.startPos, type: 'start'},
        {position: tag.endPos, type: 'end'}
      ];
    }, [])
    .uniqWith(_.isEqual)
    .orderBy(['position', 'type'], ['asc', 'desc'])
    .reduce((sum, node, i, nodes) => {
      if (i < nodes.length - 1) {
        const nextNode = nodes[i + 1];
        const start = node.type === 'start' ? node.position : node.position + 1;
        const end = nextNode.type === 'start' ? nextNode.position - 1 : nextNode.position;
        return [...sum, {start, end}];
      } else {
        return sum;
      }
    }, [])
    .value();
}

function reportsFromTags(tags) {
  return _(tags)
    .map('report')
    .uniq()
    .sortBy()
    .value();
}

const styleMap = {
  grammar: {
    background: 'linear-gradient(0deg, #F8B7B7 2px, white 1px, transparent 1px)',
  },
  consistency: {
    background: 'linear-gradient(0deg, #BA95E3 2px, white 1px, transparent 1px)',
    lineHeight: '3.1em',
    paddingBottom: '0.3em'
  },
  slength: {
    background: 'linear-gradient(0deg, #F8E71C 2px, white 1px, transparent 1px)',
    lineHeight: '3.4em',
    paddingBottom: '0.6em'
  },
  dva: {
    background: 'linear-gradient(0deg, #9013FE 2px, white 1px, transparent 1px)',
    lineHeight: '3.7em',
    paddingBottom: '0.9em'
  },
  passive: {
    background: 'linear-gradient(0deg, #B8E887 2px, white 1px, transparent 1px)',
    lineHeight: '4em',
    paddingBottom: '1.2em'
  }
};

const ReportSpan = ({tags, reports, level, text, onClick, children}) => {
  const report = reports[level];
  const filteredTags = tags.filter(t => t.report === report);
  return (
    <span className="aid" style={styleMap[report]} onClick={onClick}>
      {level !== reports.length - 1 &&
        <ReportSpan
          tags={filteredTags}
          reports={reports}
          level={level + 1}
        >
          {children}
        </ReportSpan>
      }
      {
        level === reports.length - 1 &&
        children
      }
    </span>
  );
};

const AidBlock = ({entityKey, tags, onClick, children}) => {
  const ts = tags || Entity.get(entityKey).getData().tags;
  return (
    <ReportSpan
      tags={ts}
      reports={reportsFromTags(ts)}
      level={0}
      onClick={onClick}
    >
      {children}
    </ReportSpan>
  );
};

export default AidBlock;



