import React from 'react';
import classNames from 'classnames';
import _ from 'lodash';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import LabelTypeConstants from '../../constants/LabelTypeConstants';
import UserTypeConstants from '../../constants/UserTypeConstants';
import NewUsersTable from './NewUsersTable';
import NotificationSystem from 'react-notification-system';
import SpinnerButton from '../widgets/SpinnerButton';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class NewUsers extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      selectedType: UserTypeConstants.STUDENT,
      rowLength: 1,
      users: [{}],
      errors: [],
      isCreating: false,
      maxUserNumber: 50
    };

    this.rowGetter = this.rowGetter.bind(this);
    this.validateData = this.validateData.bind(this);
    this.handleCreate = this.handleCreate.bind(this);
    this.handleClickAddRow = this.handleClickAddRow.bind(this);
    this.handleChangeInput = this.handleChangeInput.bind(this);
    this.handleRemove = this.handleRemove.bind(this);
  }

  observe(props, state) {
    return {
      classes: new Parse.Query('Label')
        .equalTo('typeRaw', LabelTypeConstants.CLASS),
      users: new Parse.Query('User')
    };
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
    Parse.Config.get().then((options) => {
      this.setState({maxUserNumber: options.attributes.maxUserNumber});
    });
  }

  rowGetter(index) {
    const user = this.state.users[index];
    return [
      user && user.email,
      user && user.displayName,
      user && user.password,
      user && user.classLabel,
    ];
  }

  handleClickAddRow() {
    const existingUsers = this.data.users;
    const { users, maxUserNumber, rowLength } = this.state;
    if (existingUsers.length && (existingUsers.length + rowLength) >= maxUserNumber) {
      this._notificationSystem.addNotification({
        message: 'You have reached your maximum active account limit. Please contact us to upgrade your account limit.',
        level: 'error',
      });
      return;
    }
    this.setState({users: users.concat({}), rowLength: this.state.rowLength + 1});
  }

  handleChangeInput(index, type, e) {
    let users = this.state.users;
    let user = this.state.users[index] || {};
    const value = e.target.value;
    switch (type) {
      case 'typeRaw':
        user[type] = parseInt(value);
        break;
      default:
        user[type] = value;
    };
    if (type === 'email') {
      if (!user.customizedPassword && !user.displayNameToPassword) {
        user.emailToPassword = true;
        user.password = value;
      };
      !user.customizedDisplayName && (user.displayName = value);
    };
    if (type === 'displayName') {
      if (!user.customizedPassword && !user.emailToPassword) {
        user.displayNameToPassword = true;
        user.password = value;
      };
      user.customizedDisplayName = true;
    };
    if (type === 'password') {
      user.customizedPassword = true;
    };

    users[index] = user;
    this.setState({users:users});
  }

  handleRemove(index) {
    const users = this.state.users.slice(0, index).concat(this.state.users.slice(index + 1, this.state.users.length + 1));
    this.setState({users, rowLength: users.length});
  }

  validateData() {
    const errors = this.state.users.map((user) => {
      let errorsArray = [];
      if (!user.displayName && !user.email) {
        errorsArray[0] = 'username / email must be set';
        errorsArray[1] = errorsArray[0];
      };
      if (!user.password) {
        errorsArray[2] = ('password must be set');
      };
      return (errorsArray.length ? errorsArray : null);
    });
    this.setState({errors});
    if (_.compact(errors).length) {
      return false;
    } else {
      return true;
    }
  }

  handleCreate() {
    if (!this.validateData()) {
      return;
    };

    this.setState({isCreating: true});
    Parse.Cloud.run('createUsers', {users: this.state.users}).then((result) => {
      this._notificationSystem.addNotification({
        message: 'Successfully created users',
        level: 'success',
      });
      this.setState({isCreating: false});
      this.context.router.push('/admin/users');
    }, (error) => {
      JSON.parse(error.message).map((e) => {
        this._notificationSystem.addNotification({
          message: e.message,
          level: 'error',
        });
      });
      this.setState({isCreating: false});
    });
  }

  render() {
    return (
      <div className="new-users grid-block vertical">
        <div className="grid-container">
          <div className="grid-block">
            <div className="grid-block shrink">
              <div
                className="button hollow rounded add"
                onClick={this.handleClickAddRow}
              >
                Add Row
              </div>
            </div>
            <div className="grid-block">
              <SpinnerButton
                className={classNames(
                  'small-3',
                  'medium-3',
                  'button',
                  'hollow',
                  'rounded',
                  'save',
                  'success',
                  {disabled: this.state.isCreating}
                  )}
                spinnerColorClass="success"
                onClick={this.handleCreate}
                isSpinning={this.state.isCreating}
              >
                Create
              </SpinnerButton>
            </div>
          </div>
          <div className="grid-block table">
            <NewUsersTable
              rowLength={this.state.rowLength}
              onChangeInput={this.handleChangeInput}
              onRemove={this.handleRemove}
              rowGetter={this.rowGetter}
              classes={this.data.classes}
              errors={this.state.errors}
            />
          </div>
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </div>
    );
  }
};

NewUsers.contextTypes = contextTypes;

export default NewUsers;




