import React from 'react';
import Draft from 'draft-js';
import Avatar from '../users/Avatar';
import AidBlock from '../draft/AidBlock';
import { insertAidBlocks } from '../../modifiers/insertAidBlocks';
import { removeAidBlocks } from '../../modifiers/removeAidBlocks';
import Hint from './Hint';
import Immutable from 'immutable';

const {
  Editor,
  EditorState,
  SelectionState,
  ContentState,
  Entity,
  Modifier,
  CompositeDecorator,
  getVisibleSelectionRect
} = Draft;

export default class PageTextArea extends React.Component {
  constructor(props) {
    super(props);

    const decorator = new CompositeDecorator([{
      strategy: this.findAidEntities,
      component: AidBlock
    }]);

    this.state = {
      editorState: EditorState.createEmpty(decorator),
      hidedHints: Immutable.Set(),
      rect: null,
      initialized: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.renderHints = this.renderHints.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    const { editorState, initialized } = this.state;
    if (nextProps.showHint && nextProps.aids && nextProps.aids !== this.props.aids) {
      this.setState({
        editorState: insertAidBlocks(editorState, nextProps.aids.tags)
      });
    }

    if (this.props.showHint !== nextProps.showHint && !nextProps.showHint) {
      this.setState({
        editorState: removeAidBlocks(editorState)
      });
    }

    if (!this.state.editorState.getCurrentContent().hasText() && !initialized && nextProps.initialValue) {
      this.setState({
        editorState: EditorState.createWithContent(ContentState.createFromText(nextProps.initialValue)),
        initialized: true
      });
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.state.editorState !== nextState.editorState ||
      this.props.aids !== nextProps.aids ||
      this.state.hidedHints !== nextState.hidedHints
    );
  }

  getEntityKeyAtSelection(selection, contentState) {
    return contentState
      .getBlockForKey(selection.getAnchorKey())
      .getEntityAt(selection.getAnchorOffset());
  }

  findAidEntities(contentBlock, callback) {
    contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        return (
          entityKey !== null &&
          Entity.get(entityKey).getType() === 'AID'
        );
      },
      callback
    );
  }

  updateEntities(aids, text) {
    const { editorState } = this.state;
    const promise = aids.tags.reduce((promise, tag, i) => {
      return promise.then(editorState => {
        return this.updateEditorState(editorState, tag);
      });
    }, Promise.resolve(editorState));

    promise.then((editorState) => {
      this.setState({
        editorState
      });
    });
  }

  handleChange(editorState) {
    let { rect, hidedHints } = this.state;
    const nextSelection = editorState.getSelection();
    if (this.state.editorState.getSelection() !== nextSelection && nextSelection.getHasFocus()) {
      hidedHints = Immutable.Set();
      rect = getVisibleSelectionRect(global);
    }
    this.setState({editorState, rect, hidedHints}, () => {
      const contentState = editorState.getCurrentContent();
      this.props.onChange(contentState.getPlainText());
    });
  }

  clear() {
    this.setState({editorState: EditorState.createEmpty()});
  }

  blockRenderer(block) {
    if (block.getType() === 'aid') {
      return {
        component: AidBlock,
      };
    }
    return null;
  }

  renderHints() {
    const { editorState } = this.state;
    const contentState = editorState.getCurrentContent();
    const selection = editorState.getSelection();
    const { rect, hidedHints } = this.state;
    if (!rect) return;

    const entityKey = contentState
      .getBlockForKey(selection.getAnchorKey())
      .getEntityAt(selection.getAnchorOffset());

    if (!entityKey) return;

    const entity = Entity.get(entityKey);
    if (entity.getType() !== 'AID') return;

    const { tags } = entity.getData();

    const { top, left } = this.refs.textarea.getBoundingClientRect();
    return (
      <div
        className="hints grid-block"
        style={{
          position: 'absolute',
          zIndex: 999,
          top: rect.top - top + 30,
          left: rect.left - left
        }}
      >
        {tags.map((tag, i) => (
          !hidedHints.has(i) &&
          <div className="hint grid-block" key={i}>
            <Hint
              category={tag.categoryDisplayName}
              hint={tag.hint}
              report={tag.report}
              onClose={this.closeTag.bind(this, i)}
            />
          </div>
        ))}
      </div>
    );
  }

  closeTag(index) {
    this.setState({
      hidedHints: this.state.hidedHints.add(index)
    });
  }

  render() {
    const { avatarUrl, title, userId } = this.props;
    const { editorState } = this.state;
    const hints = this.renderHints();

    return (
      <div className="textarea-container grid-block small-2-up" ref="textarea">
        <div className="small-3 align-center">
          <Avatar
            imageUrl={avatarUrl}
            title={title}
            userId={userId}
          />
        </div>
        <div className="grid-block textarea small-20">
          <Editor
            ref="editor"
            onChange={this.handleChange}
            editorState={editorState}
          />
        </div>
        {hints}
      </div>
    );
  }
}

PageTextArea.propTypes = {
  avatarUrl: React.PropTypes.string,
  placeholder: React.PropTypes.string,
  value: React.PropTypes.string,
  onChange: React.PropTypes.func,
  showHint: React.PropTypes.bool,
};

PageTextArea.defaultProps = {
  avatarUrl: require('../../assets/images/avatar.png'),
  placeholder: 'What happens next?',
  value: null,
  showHint: false,
};


