import React, { Component } from 'react';
import ReactNofiticationSystem from 'react-notification-system';

class NotificationSystem extends Component {
  constructor(props) {
    super(props);

    this.addNotification = this.addNotification.bind(this);
  }

  shouldComponentUpdate() {
    return false;
  }

  addNotification(options) {
    this.refs.notificaitonSystem.addNotification(options);
  }

  render() {
    return (
      <ReactNofiticationSystem ref="notificaitonSystem" />
    );
  }
}

export default NotificationSystem;



