import React from 'react';
import { Link } from 'react-router';
import SearchForm from './SearchForm';
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import NotificationCenter from './NotificationCenter';
import SelectedLabels from '../stories/SelectLabels';
import SearchSelect from './SearchSelect';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class SearchNavigationBar extends React.Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleChange(value) {
    if (value == '') {
      this.context.router.replace('/stories');
    } else {
      this.context.router.replace({pathname: '/stories/search', query: value});
    }
  }

  handleClick(e) {
    this.setState({value: null});
  }

  render() {
    return (
      <TitleBar>
        <TitleBar.Left>
          <Link to="/" onClick={this.handleClick}>
            <img src={require('../../assets/images/storyappylogo.svg')} />
          </Link>
        </TitleBar.Left>
        <TitleBar.Center>
          <div className="grid-block align-center">
            <div className="grid-block">
              <div className="grid-content search">
                <SearchSelect
                  onChange={this.handleChange}
                  value={this.props.location && this.props.location.query.labels}
                />
              </div>
            </div>
            <div className="medium-6 grid-block">
              <Link to="/stories/new" className="create-story button hollow">
                Create Story
              </Link>
            </div>
          </div>
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            <div className="grid-block">
              <NotificationCenter />
            </div>
            <div className="grid-block">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
      </TitleBar>
    );
  }
}

SearchNavigationBar.contextTypes = contextTypes;

export default SearchNavigationBar;




