import _ from 'lodash';

export default function format (content) {
  if (content && content.length) {
    let result = _(content).trimEnd().split('\n');

    if (_.compact(result).length) {
      result = result.reduce((sum, text) => {
        if (text.length) {
          return `${sum}<p>${text.replace('\n', '<p><br/></p>')}</p>`;
        } else {
          return `${sum}<p><br/></p>`;
        };
      }, '');
    }

    if (content.indexOf('\n\n') !== -1) {
      result = result + '<p><br/></p>';
    }
    return result;
  } else {
    return content;
  };
}



