import React from 'react';
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import NotificationCenter from './NotificationCenter';

export default class NewStoryNavigationBar extends React.Component {
  render() {
    return (
      <TitleBar>
        <TitleBar.Left>
          <BackButton />
        </TitleBar.Left>
        <TitleBar.Center>
          New Story
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            <div className="grid-block">
              <NotificationCenter />
            </div>
            <div className="grid-block">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
      </TitleBar>
    );
  }
}



