import React from 'react';
import classNames from 'classnames';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import NotificationCenter from './NotificationCenter';
import SpinnerButton from '../widgets/SpinnerButton';
import NotificationSystem from 'react-notification-system';
import UserTypeConstants from '../../constants/UserTypeConstants';
import hello from 'hello';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class StoryNavigationBar extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      isDeleting: false,
      isExportingDrive: false,
      askUserAboutDeleting: false,
    };

    this.handleDelete = this.handleDelete.bind(this);
    this.handleExport = this.handleExportPDF.bind(this);
    this.handleExportDrive = this.handleExportDrive.bind(this);
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
  }

  observe() {
    return {
      stories: new Parse.Query('Story').equalTo('objectId', this.props.params.id),
      user: ParseReact.currentUser,
    };
  }

  handleDelete() {
    if (!this.state.askUserAboutDeleting) {
      this.setState({askUserAboutDeleting: true});
      this._notificationSystem.addNotification({
        message: 'Are you sure you want to delete this story? If so, click Delete Story again',
        level: 'error',
        position: 'tc'
      });
    } else {
      this.setState({isDeleting: true, askUserAboutDeleting: false});

      ParseReact.Mutation.Set(this.data.stories[0], {
        deleted: true
      }).dispatch().then(() => {
        this.context.router.push('/stories');
      }, (error) => {
        console.log('delete story error', error);
      });
    }
  }

  handleExportPDF() {
    window.focus();
    window.print();
  }


  handleExportDrive() {
    
    this.setState({isExportingDrive: true});

    var content = [];
    const story = this.data.stories[0];

    //get content from story & format to .txt
    const rawContent = document.getElementsByClassName('content');
    for(var i = 0;i < rawContent.length; i++) {
      content.push(rawContent[i].innerText);
    }
    var joinedContent = content.join('\r\n');
    var fileDataConverted = new Blob([joinedContent], {type: 'text/plain;charset=utf-8'});
    
    //Hello.js API - first, get list of folders. 
    var google = hello('google');
    
    google.api('me/folders', 'get', {
      name: 'StoryAppy'
    }).then( response => {
      console.log('Folders response: ', response);
      
      //check if StoryAppy folder exists
      if (response.data.map( (x) => { return x.name; }).indexOf('StoryAppy') === -1) {
        
        //doesn't exist.  Create new SA folder
        google.api('me/folders', 'post', {
          name: 'StoryAppy'
        }).then( folder => {
            this._notificationSystem.addNotification({
              message: 'Create StoryAppy Folder in Google Drive',
              level: 'success'
            });

          //upload file to google drive under new folder
          return google.api('me/files', 'post', {
            parent: folder.id,
            file: [fileDataConverted],
            name: story.title
          });
        }).then( file => {
          console.log('post success', file);
          this._notificationSystem.addNotification({
            message: 'File uploaded to Google Drive in StoryAppy folder',
            level: 'success'
          });

          this.setState({isExportingDrive: false});
        }, error => {
          console.log('post file error', error);
          this._notificationSystem.addNotification({
            message: 'Error uploading file.  Please contact StoryAppy admin',
            level: 'error'
          });
          this.setState({isExportingDrive: false});
        });          
      } else {
       //StoryAppy folder exists, add file to existing folder
        var folder = response.data.filter((x) => x.name == 'StoryAppy');
        if (folder) {
          google.api('me/files', 'post', {
            parent: folder[0].id,
            file: [fileDataConverted],
            name: story.title
          }).then( file => {
            console.log('post success :',file);
            this._notificationSystem.addNotification({
              message: 'File uploaded to Google Drive in StoryAppy folder',
              level: 'success'
            });
            this.setState({isExportingDrive: false});
          }, error => {
            console.log('post file error :', error);
            this._notificationSystem.addNotification({
              message: 'Error uploading file.  Please contact StoryAppy admin',
              level: 'error'
            });
            this.setState({isExportingDrive: false});
          });
        }  
      }  
    }, error => {
      console.log('get folders error', error);
      this._notificationSystem.addNotification({
        message: 'Error logging into Google Drive. Try logging into google again or contact Admin',
        level: 'error'
      });
      this.setState({isExportingDrive: false});
    });
  }
  
  render() {
    const story = this.data.stories[0];
    let deleteButton, driveButton, pdfButton;
    if (story && this.data.user && this.data.user.typeRaw === UserTypeConstants.TEACHER) {
      driveButton = (
        <div className="grid-block">
          <SpinnerButton
            className="button hollow"
            onClick={this.handleExportDrive}
            isSpinning={this.state.isExportingDrive}
            >Drive
          </SpinnerButton>
        </div>
      );

      pdfButton = (
        <div className="grid-block">
          <SpinnerButton
            className="button hollow"
            onClick={this.handleExportPDF}
            >PDF
          </SpinnerButton>
        </div>
      );

      deleteButton = (
        <div className="grid-block">
          <SpinnerButton
            className="button hollow"
            isSpinning={this.state.isDeleting}
            onClick={this.handleDelete}
          >Delete Story
          </SpinnerButton>
        </div>
      );
    }

    return (
      <TitleBar className="delete-story">
        <TitleBar.Left>
          <BackButton to="/stories"/>
        </TitleBar.Left>
        <TitleBar.Center>
          <div className="grid-block v-align align-center">
            {story && story.title}
          </div>
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            {driveButton}
            {pdfButton}
            {deleteButton}
            <div className="grid-block align-center">
              <NotificationCenter />
            </div>
            <div className="grid-block align-center">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
        <NotificationSystem ref="notificationSystem" />
      </TitleBar>
    );
  }
}

StoryNavigationBar.contextTypes = contextTypes;

export default StoryNavigationBar;



