import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
const {Motion, spring, presets} = require('react-motion');
import classNames from 'classnames';

import SlickSlider from './SlickSlider';
import Page from './Page';
import PageStatusConstants from '../../constants/PageStatusConstants';
import UserTypeConstants from '../../constants/UserTypeConstants';

export default class PageSlider extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      savedPages: props.savedPages
    };
    this.data = {
      pages: props.pages,
      likes: props.likes
    };
  }

  observe(props) {
    if (props.refPageId == undefined || props.refPageId === '') return {};
    let pageQuery = new Parse.Query('Page')
        .equalTo('refPageId', props.refPageId)
        .ascending('createdAt').include('creator');

    return {
      pages: pageQuery,
      aids: new Parse.Query('Aid').matchesQuery('page', pageQuery),
      user: ParseReact.currentUser,
    };
  }

  handleContentUpdate(object, value) {
    ParseReact.Mutation.Set(object, value).dispatch();
  }

  render() {
    let pages;
    if (this.props.isEditing) {
      pages = [{...this.props, statusRaw: PageStatusConstants.HISTORY}];
    } else {
      pages = this.data.pages;
    }
    return (
      <Motion defaultStyle={{height: 0}} style={{height: spring(100, presets.stiff)}}>
        {value =>
          <div className="page-slider-wrapper">
            <div className="content-wrapper" style={{height: `${value.height * 4.2}px`}}>
              <SlickSlider settings={{
                className: 'page-slider-slick',
                slidesToShow: 1
              }}>
                {pages.map((p,i) => {
                  let approvedPageClass = classNames({
                    'approved': p.statusRaw == 1
                  });

                  const likedPages = this.data.likes &&
                    this.data.likes.filter((like) => {
                      return like.page !== undefined
                              && like.page.id.objectId === p.id.objectId;
                    });

                  const { user, aids } = this.data;
                  const filteredAids = aids && aids.filter((a) => a.page && a.page.objectId === p.objectId);
                  return (
                    <div key={p.objectId} className={approvedPageClass}>
                      <Page {...p}
                            key={p.objectId}
                            isLiked={likedPages.length > 0}
                            writeAssistOn={this.props.writeAssistOn}
                            activeReports={this.props.activeReports}
                            currentUser={this.data.user}
                            aids={filteredAids}
                            allowEdit={user && user.typeRaw === UserTypeConstants.TEACHER}
                            onContentUpdate={this.handleContentUpdate.bind(this, p)}
                          />
                    </div>
                  );
                }, this)}
              </SlickSlider>
            </div>
            {!this.props.isEditing &&
              <div
                className="close-slider-button button"
                onClick={this.props.onClose}
              >
                Close
              </div>
            }
          </div>
        }
      </Motion>
    );
  }
}

PageSlider.propTypes = {
  pages: React.PropTypes.array.isRequired,
  likes: React.PropTypes.array.isRequired,
  onClose: React.PropTypes.func.isRequired,
};

PageSlider.defaultProps = {
  pages: [],
  likes: []
};



