import {
  EditorState,
  Modifier,
  SelectionState
} from 'draft-js';

export function removeAidBlocks(editorState) {
  const content = editorState.getCurrentContent();
  const selection = editorState.getSelection();

  const withoutAid = content.getBlocksAsArray().reduce((content, block) => {
    const targetRange = new SelectionState({
      anchorKey: block.getKey(),
      anchorOffset: 0,
      focusKey: block.getKey(),
      focusOffset: block.getLength(),
    });

    return Modifier.applyEntity(content, targetRange, null);
  }, content);

  const newState = EditorState.push(editorState, withoutAid, 'remove-aid');
  return EditorState.forceSelection(newState, withoutAid.getSelectionAfter());
}



